import {
  MDBBtn,
  MDBCardBody,
  MDBIcon,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBTooltip,
  MDBView,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import find from "../../../../../services/utilities/axioKit/find";
import { socket, useCountdown } from "../../../../../services/utilities";
import { UPDATE as UPDATE_APPOINTMENTS } from "../../../../../services/redux/slices/appointments";
import { UPDATE as UPDATE_SCHEDULES } from "../../../../../services/redux/slices/schedules";
import Swal from "sweetalert2";
import { useHistory } from "react-router";

export default function Proctor({
  schedules,
  setSchedules,
  selectedExam,
  selectedSchedule,
  setIndex,
  didStart,
}) {
  const [examinees, setExaminees] = useState([]);
  const { token } = useSelector(({ persons }) => persons);
  const history = useHistory();

  const dispatch = useDispatch();
  const { hours, minutes, seconds } = useCountdown(
    new Date(selectedSchedule.end)
  );

  useEffect(() => {
    socket.on("start_exam", (data) => {
      const { _index, _schedule } = data;

      if (data._schedule?._id === selectedSchedule._id) {
        setIndex(_index);

        const _schedules = [...schedules];
        _schedules[_index] = _schedule;
        localStorage.setItem(
          "selectedSchedule",
          JSON.stringify(_schedules[_index])
        );

        setSchedules(_schedules);
      }
    });

    return () => {
      socket.off("start_exam");
    };
  }, [setSchedules, schedules, setIndex, selectedSchedule]);

  useEffect(() => {
    if (token && selectedSchedule) {
      const handleExaminees = async () => {
        const _examinees = await find(
          "appointments",
          "find",
          "schedule",
          selectedSchedule._id,
          token
        );

        setExaminees(
          _examinees.filter((appointment) => appointment.status === "Approved")
        );
      };

      handleExaminees();
    }
  }, [token, selectedSchedule]);

  const handleStartExam = () => {
    Swal.fire({
      icon: "info",
      title: "Are you sure?",
      text: "You mustn't leave after you started the exam",
      confirmButtonText: "Okay",
      showCancelButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(
          UPDATE_SCHEDULES({
            schedule: {
              _id: selectedSchedule._id,
              didStart: true,
            },
            token,
          })
        );
      }
    });
  };

  useEffect(() => {
    if (hours === 0 && minutes === 0 && seconds === 0) {
      Swal.fire({
        icon: "info",
        title: "Exam Ended!",
        confirmButtonText: "Proceed",
      }).then((result) => {
        if (result.isConfirmed) {
          dispatch(
            UPDATE_SCHEDULES({
              schedule: {
                _id: selectedSchedule._id,
                didFinish: true,
              },
              token,
            })
          );

          for (const index in examinees) {
            if (!examinees[index]?.attendance) {
              dispatch(
                UPDATE_APPOINTMENTS({
                  updateFields: {
                    _id: examinees[index]?._id,
                    status: "Failed",
                  },
                  token,
                })
              );
            }
          }

          history.push("/schedules");
        }
      });
    }
  }, [
    hours,
    minutes,
    seconds,
    selectedSchedule,
    token,
    dispatch,
    examinees,
    history,
  ]);

  useEffect(() => {
    socket.on("recieve_attendance", (appointment) => {
      const updatedExaminees = examinees.map((examinee) => {
        if (examinee._id === appointment._id) {
          return {
            ...examinee,
            attendance: appointment.attendance,
          };
        }
        return examinee;
      });

      setExaminees(updatedExaminees);
    });

    return () => {
      socket.off("recieve_attendance");
    };
  }, [examinees]);

  return (
    <div>
      <MDBView
        cascade
        className="gradient-card-header blue-gradient narrower py-2
        d-flex justify-content-between align-items-center"
      >
        <a href="#!" className="white-text">
          {selectedExam?.title} Exam {!didStart ? "Examinees" : "Started"}
        </a>

        {!didStart ? (
          <MDBTooltip placement="left">
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2"
              onClick={handleStartExam}
            >
              <MDBIcon icon="play" className="mt-0" />
            </MDBBtn>
            <div>Start Exam</div>
          </MDBTooltip>
        ) : (
          <div className="p-2">
            Time Left: {String(hours).padStart(2, "0")}:
            {String(minutes).padStart(2, "0")}:
            {String(seconds).padStart(2, "0")}
          </div>
        )}
      </MDBView>
      <MDBCardBody>
        {examinees.length === 0 ? (
          <div className="text-center">No Examinees Found..</div>
        ) : (
          <MDBTable responsive hover className="text-center">
            <MDBTableHead>
              <tr>
                <th>#</th>
                <th className="th-lg">Username</th>
                <th className="th-lg">Email</th>
                <th className="th-lg">Attendance</th>
              </tr>
            </MDBTableHead>

            <MDBTableBody>
              {examinees.map((examinee, index) => (
                <tr key={index}>
                  <td>{index + 1}.</td>
                  <td>{examinee?.examinee?.username}</td>
                  <td>{examinee?.examinee?.email}</td>
                  <td>{examinee.attendance ? "Present" : "Absent"}</td>
                </tr>
              ))}
            </MDBTableBody>
          </MDBTable>
        )}
      </MDBCardBody>
    </div>
  );
}
