import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { axioKit, socket } from "../../utilities";

const name = "appointments";

const initialState = {
  appointments: [],
  isSuccess: false,
  isLoading: false,
  message: "",
  _id: "",
  selectedAppointment:
    JSON.parse(localStorage.getItem("selectedAppointment")) || null,
};

export const BROWSE = createAsyncThunk(`${name}`, (token, thunkAPI) => {
  try {
    return axioKit.browse(name, token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const SAVE = createAsyncThunk(`${name}/save`, (form, thunkAPI) => {
  try {
    axioKit.save(name, form.appointment, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const UPDATE = createAsyncThunk(`${name}/update`, (form, thunkAPI) => {
  try {
    return axioKit.update(name, form.updateFields, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.isSuccess = false;
      state.message = "";
    },
    SETID: (state, data) => {
      state._id = data.payload;
    },
    SELECTED: (state, data) => {
      state.selectedAppointment = data.payload;
      localStorage.setItem("selectedAppointment", JSON.stringify(data.payload));
    },
  },
  extraReducers: (builder) => {
    builder

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        state.appointments.unshift(action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(UPDATE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(UPDATE.fulfilled, (state, action) => {
        const index = state.appointments.findIndex(
          (appointment) => appointment._id === action.payload._id
        );

        state.appointments[index] = action.payload;

        socket.emit("send_attendance", action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(UPDATE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(BROWSE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(BROWSE.fulfilled, (state, action) => {
        state.appointments = action.payload;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(BROWSE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const { RESET, SETID, SELECTED, UPDATEATTENDANCE } = reduxSlice.actions;

export default reduxSlice.reducer;
