import React, { useState, useEffect } from "react";
import {
  monthDayYearFormat,
  timeFormat,
} from "../../../../services/utilities/timeAndDateFormat";
import { MDBBtn, MDBIcon, MDBTooltip } from "mdbreact";
import { useHistory } from "react-router";
import Swal from "sweetalert2";
import { useDispatch } from "react-redux";
import { SELECTEDEXAM } from "../../../../services/redux/slices/banks";
import { SELECTED } from "../../../../services/redux/slices/schedules";

export default function RowSchedule({ schedule, index }) {
  const [remainingSlots, setRemainingSlots] = useState(0);
  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    setRemainingSlots(schedule.maxSlots - schedule.slotsTaken);
  }, [schedule]);

  const handleTakeExam = () => {
    if (new Date() >= new Date(schedule.start)) {
      dispatch(SELECTEDEXAM(schedule.exam));
      dispatch(SELECTED(schedule));
      history.push("/exam");
    } else {
      Swal.fire({
        icon: "info",
        title: "Exam Not Available",
        text: "Please check back on the exact exam day/time.",
        confirmButtonText: "OK",
      });
    }
  };

  return (
    <tr>
      <th>{index + 1}.</th>
      <td>{schedule.title}</td>
      <td>
        {monthDayYearFormat(schedule.start)} <br />
        {timeFormat(schedule.start)}
      </td>
      <td>
        {monthDayYearFormat(schedule.end)} <br />
        {timeFormat(schedule.end)}
      </td>
      <td>{schedule.exam?.title}</td>
      <td>
        {remainingSlots}/{schedule.maxSlots}
      </td>
      <td>
        <MDBTooltip placement="right">
          <MDBBtn
            outline
            color="blue"
            rounded
            size="sm"
            className="px-2"
            onClick={handleTakeExam}
          >
            <MDBIcon icon="arrow-right" className="mt-0" />
          </MDBBtn>
          <div>Take Exam</div>
        </MDBTooltip>
      </td>
    </tr>
  );
}
