import Schedules from "./schedules";
import Banks from "./banks";
import Dashboard from "./dashboard";
import Exams from "./exams";
import Persons from "./persons";
import Scores from "./scores";
import Exam from "../examinee/exam";

const routes = [
  {
    path: "/dashboard",
    component: Dashboard,
  },
  {
    path: "/exams",
    component: Exams,
  },
  {
    path: "/exam",
    component: Exam,
  },
  {
    path: "/exams/banks",
    component: Banks,
  },
  {
    path: "/persons",
    component: Persons,
  },
  {
    path: "/scores",
    component: Scores,
  },
  {
    path: "/schedules",
    component: Schedules,
  },
];

export default routes;
