export function monthDayYearFormat(dateString) {
  return new Date(dateString).toLocaleDateString();
}

export function monthYearFormat(dateString) {
  return new Date(dateString).toLocaleDateString(undefined, {
    year: "numeric",
    month: "long",
  });
}

export function timeFormat(timeString) {
  return new Date(timeString).toLocaleTimeString(undefined, {
    hour: "numeric",
    minute: "numeric",
    hour12: true,
  });
}
