import {
  MDBContainer,
  MDBBtn,
  MDBInput,
  MDBCardBody,
  MDBCard,
  MDBView,
  MDBMask,
  MDBRow,
  MDBCol,
  MDBIcon,
  MDBSelect,
  MDBSelectInput,
  MDBSelectOptions,
  MDBSelectOption,
} from "mdbreact";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { SAVE } from "../../services/redux/slices/persons";

export default function Registration() {
  const [person, setPerson] = useState({
    username: "",
    email: "",
    password: "",
    confirmPassword: "",
    role: "",
    dob: "",
  });

  const roles = [
    { text: "Receptionist", value: "RECEPTIONIST" },
    { text: "Examinee", value: "EXAMINEE" },
  ];

  const history = useHistory();
  const dispatch = useDispatch();

  const handleChange = (key, value) =>
    setPerson({
      ...person,
      [key]: value,
    });

  const handleReset = () => {
    setPerson({
      username: "",
      email: "",
      password: "",
      confirmPassword: "",
      role: "",
      dob: "",
    });
  };

  const handleSignUp = (e) => {
    e.preventDefault();

    if (person.password !== person.confirmPassword)
      return alert("Passwords do not match");

    if (person.role === "")
      return alert("Please choose a valid role from the predefined options.");

    dispatch(SAVE(person));
    handleReset();
    history.push("/");
  };

  return (
    <section id="register">
      <MDBView>
        <MDBMask
          className="d-flex justify-content-center align-items-center"
          overlay="stylish-strong"
        >
          <MDBContainer>
            <MDBRow>
              <MDBCol md="10" lg="8" sm="11" className="mt-4 mx-auto">
                <MDBCard>
                  <MDBCardBody>
                    <div className="form-header blue-gradient">
                      <h3>
                        <MDBIcon icon="user" className="mt-2 mb-2 text-white" />{" "}
                        Create Account
                      </h3>
                    </div>
                    <form onSubmit={handleSignUp}>
                      <MDBRow>
                        <MDBCol sm="12" md="6">
                          <MDBInput
                            type="text"
                            label="Username"
                            icon="user"
                            iconClass="white-text"
                            value={person.username}
                            onChange={(e) =>
                              handleChange("username", e.target.value)
                            }
                            required
                          />
                        </MDBCol>
                        <MDBCol sm="12" md="6">
                          <MDBInput
                            type="email"
                            label="Email"
                            icon="envelope"
                            iconClass="white-text"
                            value={person.email}
                            onChange={(e) =>
                              handleChange("email", e.target.value)
                            }
                            required
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow>
                        <MDBCol sm="12" md="6">
                          <MDBInput
                            type="password"
                            label="Password"
                            icon="lock"
                            iconClass="white-text"
                            value={person.password}
                            onChange={(e) =>
                              handleChange("password", e.target.value)
                            }
                            required
                          />
                        </MDBCol>
                        <MDBCol sm="12" md="6">
                          <MDBInput
                            type="password"
                            label="Confirm Password"
                            icon="lock"
                            iconClass="white-text"
                            value={person.confirmPassword}
                            onChange={(e) =>
                              handleChange("confirmPassword", e.target.value)
                            }
                            required
                          />
                        </MDBCol>
                      </MDBRow>
                      <MDBRow>
                        <MDBCol sm="12" md="6">
                          {" "}
                          <MDBInput
                            type="date"
                            label="Date of Birth"
                            icon="calendar"
                            iconClass="white-text"
                            value={person.dob}
                            onChange={(e) =>
                              handleChange("dob", e.target.value)
                            }
                            required
                          />
                        </MDBCol>
                        <MDBCol sm="12" md="6">
                          <div className="d-flex align-items-center">
                            <MDBIcon
                              icon="user-edit"
                              className="text-white mr-2 fa-lg"
                            />
                            <MDBSelect
                              className="w-100"
                              label="Select Role"
                              labelClass="white-text"
                              required
                              getValue={(value) =>
                                handleChange("role", value[0])
                              }
                            >
                              <MDBSelectInput selected={person.role} />
                              <MDBSelectOptions>
                                <MDBSelectOption disabled>
                                  Select Role
                                </MDBSelectOption>
                                {roles.map((option) => (
                                  <MDBSelectOption
                                    key={option.value}
                                    value={option.value}
                                  >
                                    {option.text}
                                  </MDBSelectOption>
                                ))}
                              </MDBSelectOptions>
                            </MDBSelect>
                          </div>
                        </MDBCol>
                      </MDBRow>

                      <div className="text-center mt-3 black-text">
                        <MDBBtn className="blue-gradient w-50" type="submit">
                          Sign Up
                        </MDBBtn>
                        <hr className="hr-light mb-3 mt-4" />
                        <a href="/" className="white-text mt-3">
                          Already have an account?
                        </a>
                      </div>
                    </form>
                  </MDBCardBody>
                </MDBCard>
              </MDBCol>
            </MDBRow>
          </MDBContainer>
        </MDBMask>
      </MDBView>
    </section>
  );
}
