import {
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalFooter,
  MDBModalHeader,
} from "mdbreact";
import React, { useEffect } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router";
import { SAVE } from "../../../../../services/redux/slices/scores";

export default function Score({ modal, answers, selectedExam, banks }) {
  const { token, auth } = useSelector(({ persons }) => persons);
  const [score, setScore] = useState(-1);
  const [message, setMessage] = useState("Congratulations");

  const history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    if (modal) {
      var score = 0;
      for (const index in banks) {
        const bank = banks[index];
        const answer = answers[index];

        if (answer === bank.answer) {
          score += 1;
        }
      }
      setScore(score);
    }
  }, [answers, modal, banks]);

  useEffect(() => {
    if (score === 0) return setMessage("Try Again!");

    if (score === banks.length) return setMessage("Wow");

    if (score >= 0.7 * banks.length) {
      setMessage("Hooray");
    } else {
      setMessage("Nice Try");
    }

    if (score > -1) {
      dispatch(
        SAVE({
          score: {
            exam: selectedExam?._id,
            examinee: auth?._id,
            score,
            bank: banks.length,
          },
          token,
        })
      );
    }
  }, [score, banks, auth, selectedExam, token, dispatch]);

  const handleFinish = () => {
    localStorage.removeItem("selectedAppointment");
    localStorage.removeItem("selectedSchedule");
    localStorage.removeItem("selectedExam");
    history.push("/exams");
  };

  return (
    <MDBModal
      size="sm"
      className={`modal-notify modal-success`}
      backdrop={true}
      isOpen={modal}
      toggle={() => {}}
    >
      <MDBModalHeader
        className="d-flex justify-content-center"
        tag="p"
        titleClass="heading lead"
      >
        Exam Result
      </MDBModalHeader>

      <MDBModalBody className="text-center">
        <h4 style={{ fontWeight: "500" }}>{message}</h4>
        <h6 style={{ fontWeight: "400" }}>
          You got {score} out of {banks.length}
        </h6>
      </MDBModalBody>
      <MDBModalFooter className="d-flex justify-content-center">
        <MDBBtn outline color="info" onClick={handleFinish}>
          Finish
        </MDBBtn>
      </MDBModalFooter>
    </MDBModal>
  );
}
