import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBCol,
  MDBContainer,
  MDBIcon,
  MDBInput,
  MDBRow,
  MDBTabContent,
  MDBTabPane,
  MDBTooltip,
  MDBView,
  toast,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import Score from "./score";
import {
  SELECTED,
  UPDATE,
} from "../../../../../services/redux/slices/appointments";
import { socket, useCountdown } from "../../../../../services/utilities";
import { useCallback } from "react";

export default function Examinee({
  selectedExam,
  banks,
  schedules,
  setSchedules,
  selectedSchedule,
  setIndex,
  didStart,
}) {
  const { selectedAppointment } = useSelector(
    ({ appointments }) => appointments
  );

  const [answers, setAnswers] = useState([]);

  const { token } = useSelector(({ persons }) => persons);
  const [modal, setModal] = useState(false);
  const dispatch = useDispatch();
  const [activeStep, setActiveStep] = useState(-1);
  const { hours, minutes, seconds } = useCountdown(
    new Date(selectedSchedule.end)
  );

  useEffect(() => {
    if (selectedAppointment) {
      localStorage.setItem(
        "selectedAppointment",
        JSON.stringify(selectedAppointment)
      );
    }
  }, [selectedAppointment]);

  const handleNext = () => {
    setActiveStep(activeStep + 1);
  };

  const handleProceed = () => {
    if (selectedAppointment.attendance === "-")
      return toast.warning("Attendance Required");

    Swal.fire({
      icon: "info",
      html:
        "<h4 style='font-weight: bold;'>Are you sure?</h4>" +
        "<h6 style='font-weight: 400;'>You can't turn back after proceeding.</h6>",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        handleNext();
      }
    });
  };

  useEffect(() => {
    socket.on("recieve_attendance", (appointment) => {
      dispatch(SELECTED(appointment));
    });

    return () => {
      socket.off("recieve_attendance");
    };
  }, [dispatch]);

  const handleAttendance = () => {
    dispatch(
      UPDATE({
        updateFields: {
          _id: selectedAppointment._id,
          attendance: true,
        },
        token,
      })
    );
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  const toggle = useCallback(() => {
    setModal(!modal);
  }, [modal]);

  useEffect(() => {
    socket.on("start_exam", (data) => {
      const { _index, _schedule } = data;

      if (
        data._schedule?._id === selectedSchedule._id &&
        data._schedule.didStart
      ) {
        setIndex(_index);

        const _schedules = [...schedules];
        _schedules[_index] = _schedule;
        localStorage.setItem(
          "selectedSchedule",
          JSON.stringify(_schedules[_index])
        );

        setSchedules(_schedules);
      }
    });

    return () => {
      socket.off("start_exam");
    };
  }, [setSchedules, schedules, setIndex, selectedSchedule]);

  useEffect(() => {
    if (hours === 0 && minutes === 0 && seconds === 0) {
      Swal.fire({
        icon: "info",
        title: "Time Out!",
        text: "Exam Ended",
        confirmButtonText: "Proceed",
      }).then((result) => {
        if (result.isConfirmed) {
          dispatch(
            UPDATE({
              updateFields: {
                _id: selectedAppointment._id,
                status: "Completed",
              },
              token,
            })
          );

          toggle();
        }
      });
    }
  }, [hours, minutes, seconds, selectedAppointment, token, dispatch, toggle]);

  useEffect(() => {
    if (didStart && activeStep !== -1) {
      window.onbeforeunload = function () {
        return "Are you sure you want to reload?";
      };
    }
  }, [didStart, activeStep]);

  const handleRadioClick = (bankIndex, choiceIndex) => {
    setAnswers(
      banks.map((_, index) =>
        index === bankIndex ? choiceIndex : answers[index]
      )
    );
  };

  const handleSubmit = () => {
    if (answers.length === 0 || answers.some((answer) => answer === undefined))
      return toast.warning("Please answer all the questions!");

    Swal.fire({
      icon: "info",
      html:
        "<h4 style='font-weight: bold;'>Are you sure?</h4>" +
        "<h6 style='font-weight: 400;'>You can't turn back after submitting.</h6>",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        toggle();
      }
    });
  };

  return (
    <MDBContainer>
      <Score
        modal={modal}
        answers={answers}
        toggle={toggle}
        selectedExam={selectedExam}
        banks={banks}
      />
      <MDBView
        cascade
        className="gradient-card-header blue-gradient narrower py-2
        } d-flex justify-content-between align-items-center"
      >
        <div style={{ padding: "13.5px" }}></div>

        <a href="#!" className="white-text">
          {selectedExam?.title} Examination
          {activeStep === -1 ? "" : ` (${banks.length} Items)`}
        </a>
        {activeStep === -1 ? (
          <MDBTooltip placement="left">
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2"
              onClick={handleAttendance}
            >
              <MDBIcon icon="user-check" className="mt-0" />
            </MDBBtn>
            <div>Click For Attendance</div>
          </MDBTooltip>
        ) : (
          <div>
            <MDBBtn
              outline
              color="white"
              rounded
              size="sm"
              className="px-2 mr-2"
            >
              <MDBIcon icon="clock" className="mt-0" />
            </MDBBtn>
            {String(hours).padStart(2, "0")}:{String(minutes).padStart(2, "0")}:
            {String(seconds).padStart(2, "0")}
          </div>
        )}
      </MDBView>

      <MDBCardBody>
        <MDBTabContent activeItem={activeStep}>
          {banks.map((bank, index) => (
            <MDBTabPane key={`tabpane-${index}`} tabId={index}>
              <MDBCard
                style={{ minHeight: "140px" }}
                color="grey lighten-3"
                className="mb-3"
              >
                <MDBCardBody>
                  {`${index + 1}. ${bank.question}`}
                  <MDBRow>
                    {bank.choices?.map((choice, choiceIndex) => (
                      <MDBCol md="6" lg="6" key={`choice-${choiceIndex}`}>
                        <MDBInput
                          onClick={() => handleRadioClick(index, choiceIndex)}
                          checked={answers[index] === choiceIndex}
                          label={`${String.fromCharCode(
                            65 + choiceIndex
                          )}. ${choice}`}
                          type="radio"
                          id={`${index}-${choiceIndex}`}
                        />
                      </MDBCol>
                    ))}
                  </MDBRow>
                </MDBCardBody>
              </MDBCard>
              <MDBRow>
                {index > 0 && (
                  <MDBCol>
                    <MDBBtn color="info" onClick={handleBack} outline>
                      Back
                    </MDBBtn>
                  </MDBCol>
                )}
                <MDBCol className="text-right">
                  <MDBBtn
                    onClick={
                      index === banks.length - 1 ? handleSubmit : handleNext
                    }
                    color={index === banks.length - 1 ? "success" : "info"}
                  >
                    {index === banks.length - 1 ? "Submit" : "Next"}
                  </MDBBtn>
                </MDBCol>
              </MDBRow>
            </MDBTabPane>
          ))}
          <MDBTabPane tabId={-1}>
            <div>
              <MDBCard
                style={{ minHeight: "140px" }}
                color="grey lighten-3"
                className="mb-3"
              >
                <MDBCardBody className="d-flex align-items-center justify-content-center">
                  <h1
                    className="text-center h1-responsive"
                    style={{ fontFamily: "monospace" }}
                  >
                    Proctor will start the exam soon.. Please Wait..
                  </h1>
                </MDBCardBody>
              </MDBCard>
              <div className="text-right">
                <MDBBtn
                  color="info"
                  onClick={handleProceed}
                  disabled={!didStart}
                >
                  Proceed
                </MDBBtn>
              </div>
            </div>
          </MDBTabPane>
        </MDBTabContent>
      </MDBCardBody>
    </MDBContainer>
  );
}
