import {
  MDBBtn,
  MDBCol,
  MDBContainer,
  MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBRow,
  toast,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  SAVE,
  UPDATE,
  UPDATEINDEX,
} from "../../../../services/redux/slices/schedules";
import { BROWSE as PERSONS_BROWSE } from "../../../../services/redux/slices/persons";
import { BROWSE as EXAMS_BROWSE } from "../../../../services/redux/slices/exams";

export default function CreateSchedule({ modal, toggle }) {
  const [schedule, setSchedule] = useState({
    title: "",
    start: "",
    end: "",
    exam: "",
    admin: "",
    maxSlots: 0,
    slotsTaken: 0,
  });
  const { isLoading, index, selectedSchedule, schedules } = useSelector(
    ({ schedules }) => schedules
  );
  const { token, persons } = useSelector(({ persons }) => persons);
  const { exams } = useSelector(({ exams }) => exams);
  const [admins, setAdmins] = useState([]);

  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(PERSONS_BROWSE(token));
      dispatch(EXAMS_BROWSE(token));
    }
  }, [token, dispatch]);

  useEffect(() => {
    if (persons) {
      setAdmins(persons.filter((person) => person.role === "ADMINISTRATOR"));
    }
  }, [persons]);

  function formatDateForInputType(dateTime) {
    const date = new Date(dateTime);
    const year = date.getFullYear();
    const month = (date.getMonth() + 1).toString().padStart(2, "0");
    const day = date.getDate().toString().padStart(2, "0");
    const hours = date.getHours().toString().padStart(2, "0");
    const minutes = date.getMinutes().toString().padStart(2, "0");

    return `${year}-${month}-${day}T${hours}:${minutes}`;
  }

  useEffect(() => {
    if (index >= 0) {
      setSchedule({
        ...selectedSchedule,
        start: formatDateForInputType(selectedSchedule.start),
        end: formatDateForInputType(selectedSchedule.end),
      });
    }
  }, [index, selectedSchedule]);

  const handleChange = (key, value) => {
    setSchedule({
      ...schedule,
      [key]: value,
    });
  };

  const handleReset = () => {
    setSchedule({
      title: "",
      start: "",
      end: "",
      exam: "",
      admin: "",
      maxSlots: 0,
      slotsTaken: 0,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (schedule.start === schedule.end)
      return toast.warn("End Date/Time and Start Date/Time cannot be equal.");

    if (schedule.start > schedule.end)
      return toast.warn(
        "End Date/Time cannot be earlier than Start Date/Time."
      );

    const updatedSchedule = {
      ...schedule,
      start: new Date(schedule.start).toLocaleString(),
      end: new Date(schedule.end).toLocaleString(),
    };

    if (schedule.maxSlots <= 0)
      return toast.error("Slots can't be zero or below.");

    if (index >= 0) {
      if (schedule.maxSlots < schedules[index].slotsTaken)
        return toast.error(
          "You can't edit the number of slots below the number of slots taken."
        );

      dispatch(UPDATE({ schedule: updatedSchedule, token }));
      dispatch(UPDATEINDEX(-1));
    } else {
      dispatch(
        SAVE({
          schedule: updatedSchedule,
          token,
        })
      );
    }

    window.location.href = "/schedules";
    toggle();
  };

  const handleToggle = () => {
    handleReset();
    dispatch(UPDATEINDEX(-1));
    toggle();
  };
  return (
    <MDBModal
      size="lg"
      className={`modal-notify modal-${index >= 0 ? "warning" : "success"}`}
      backdrop={true}
      isOpen={modal}
      toggle={() => {}}
    >
      <MDBModalHeader tag="p" toggle={handleToggle} titleClass="heading lead">
        {index >= 0 ? "Update Schedule" : "Create Schedule"}
      </MDBModalHeader>

      <MDBModalBody>
        <form onSubmit={handleSubmit} className="text-left">
          <MDBRow>
            <MDBCol sm="12" md="6">
              <MDBInput
                type="text"
                label="Title"
                value={schedule.title}
                onChange={(e) => handleChange("title", e.target.value)}
                required
              />
            </MDBCol>
            <MDBCol sm="12" md="6">
              <MDBInput
                type="number"
                label="Slots"
                value={schedule.maxSlots}
                onChange={(e) => handleChange("maxSlots", e.target.value)}
                required
              />
            </MDBCol>
          </MDBRow>

          <MDBRow>
            <MDBCol sm="12" md="6" style={{ marginBottom: "27.5px" }}>
              <small className="grey-text">Start Date/Time</small>
              <br />
              <input
                className="mt-2  form-control"
                type="datetime-local"
                value={schedule.start}
                onChange={(e) => handleChange("start", e.target.value)}
                required
                min={formatDateForInputType(new Date())}
              />
            </MDBCol>
            <MDBCol sm="12" md="6" style={{ marginBottom: "25.5px" }}>
              <small className="grey-text">End Date/Time</small>
              <br />
              <input
                className="mt-2 form-control"
                type="datetime-local"
                value={schedule.end}
                onChange={(e) => handleChange("end", e.target.value)}
                required
                min={formatDateForInputType(new Date())}
              />
            </MDBCol>
          </MDBRow>

          <MDBRow>
            <MDBCol sm="12" md="6" style={{ marginBottom: "25.5px" }}>
              <small className="grey-text">Proctor</small>
              <br />
              <select
                className="mt-2"
                value={index >= 0 ? selectedSchedule.admin._id : schedule.admin}
                onChange={(e) => handleChange("admin", e.target.value)}
                required
              >
                <option value="" disabled>
                  Select Proctor
                </option>
                {admins.map((admin) => (
                  <option key={admin._id} value={admin._id}>
                    {admin.username}
                  </option>
                ))}
              </select>
            </MDBCol>
            <MDBCol sm="12" md="6">
              <small className="grey-text">Exam</small>
              <br />
              <select
                className="mt-2"
                value={index >= 0 ? selectedSchedule.exam._id : schedule.exam}
                onChange={(e) => handleChange("exam", e.target.value)}
                required
              >
                <option value="" disabled>
                  Select Exam
                </option>
                {exams.map((exam) => (
                  <option key={exam._id} value={exam._id}>
                    {exam.title}
                  </option>
                ))}
              </select>
            </MDBCol>
          </MDBRow>

          <MDBContainer className="text-center">
            <MDBBtn
              disabled={isLoading}
              type="submit"
              color={`${index >= 0 ? "warning" : "success"}`}
              className="w-40"
            >
              {index >= 0 ? "Update" : "Submit"}
            </MDBBtn>
          </MDBContainer>
        </form>
      </MDBModalBody>
    </MDBModal>
  );
}
