import { MDBCard, MDBContainer } from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Examinee from "./examinee";
import Proctor from "./proctor";
import { socket } from "../../../../services/utilities";
import find from "../../../../services/utilities/axioKit/find";
import { BROWSE as APPOINTMENTS_BROWSE } from "../../../../services/redux/slices/appointments";
import { BROWSE as SCHEDULE_BROWSE } from "../../../../services/redux/slices/schedules";

export default function Exam() {
  const { selectedSchedule } = useSelector(({ schedules }) => schedules);
  const { auth, token } = useSelector(({ persons }) => persons);
  const [schedules, setSchedules] = useState([]);
  const { selectedExam } = useSelector(({ banks }) => banks);
  const [didStart, setDidStart] = useState(false);
  const dispatch = useDispatch();
  const [banks, setBanks] = useState([]);
  const [index, setIndex] = useState(-1);

  useEffect(() => {
    if (token) {
      dispatch(APPOINTMENTS_BROWSE(token));
      dispatch(SCHEDULE_BROWSE(token));
    }
  }, [token, dispatch]);

  useEffect(() => {
    if (selectedExam?._id) {
      const handleBanks = async () => {
        const banks = await find(
          "banks",
          "find",
          "exam",
          selectedExam._id,
          token
        );

        setBanks(banks.sort(() => Math.random() - 0.5));

        localStorage.setItem("selectedExam", JSON.stringify(selectedExam));
      };

      handleBanks();
    }
  }, [dispatch, selectedExam, token]);

  useEffect(() => {
    if (selectedSchedule?._id) {
      const handleBanks = async () => {
        const _schedules = await find(
          "schedules",
          "find",
          "admin",
          `${
            selectedSchedule.admin?._id === undefined
              ? selectedSchedule.admin
              : selectedSchedule.admin?._id
          }`,
          token
        );

        setSchedules(_schedules);

        localStorage.setItem(
          "selectedSchedule",
          JSON.stringify(selectedSchedule)
        );
      };

      handleBanks();
    }
  }, [dispatch, selectedSchedule, token]);

  useEffect(() => {
    socket.emit("join_room", selectedSchedule?._id);
  }, [selectedSchedule]);

  useEffect(() => {
    if (index === -1) {
      setDidStart(selectedSchedule?.didStart);
    } else {
      setDidStart(schedules[index]?.didStart);
    }
  }, [selectedSchedule, index, schedules]);

  return (
    <MDBContainer>
      <MDBCard narrow>
        {auth.role === "EXAMINEE" ? (
          <Examinee
            schedules={schedules}
            setSchedules={setSchedules}
            selectedExam={selectedExam}
            banks={banks}
            selectedSchedule={selectedSchedule}
            setIndex={setIndex}
            didStart={didStart}
          />
        ) : (
          <Proctor
            schedules={schedules}
            setSchedules={setSchedules}
            selectedExam={selectedExam}
            banks={banks}
            selectedSchedule={selectedSchedule}
            setIndex={setIndex}
            didStart={didStart}
          />
        )}
      </MDBCard>
    </MDBContainer>
  );
}
