import axios from "axios";

const browse = async (entity, token) =>
  await axios
    .get(`${entity}/browse`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    .then(({ data }) => {
      const { payload } = data;
      return payload;
    })
    .catch(({ response }) => {
      const { error } = response.data;
      throw new Error(error);
    });

export default browse;
