import { MDBModal, MDBModalBody, MDBModalHeader, MDBTable } from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import RowAppointment from "./row";
import find from "../../../../../services/utilities/axioKit/find";

export default function CreateAppointment({ modal, toggle }) {
  const { token } = useSelector(({ persons }) => persons);
  const { _id } = useSelector(({ schedules }) => schedules);
  const dispatch = useDispatch();
  const [schedules, setSchedules] = useState([]);

  useEffect(() => {
    if (token && modal) {
      const handleSchedules = async () => {
        setSchedules(await find("schedules", "find", "exam", _id, token));
      };

      handleSchedules();
    }
  }, [dispatch, token, _id, modal]);

  return (
    <MDBModal
      size="xl"
      className="modal-notify modal-info"
      backdrop={true}
      isOpen={modal}
      toggle={() => {}}
    >
      <MDBModalHeader
        toggle={toggle}
        titleClass="heading lead"
        className="d-flex justify-content-center"
      >
        <div
          style={{ fontWeight: 450, fontFamily: "inherit", fontSize: "21px" }}
        >
          {schedules[0]?.exam?.title} Examination Schedules
        </div>
      </MDBModalHeader>

      <MDBModalBody className="green">
        {schedules.length === 0 ? (
          <div className="text-center black-text">
            No Available Schedules For This Exam..
          </div>
        ) : (
          <div>
            <MDBTable responsive hover>
              <thead className=" mdb-color darken-4">
                <tr className="white-text">
                  <th>#</th>
                  <th className="th-lg">Title</th>
                  <th className="th-lg">Start</th>
                  <th className="th-lg">End</th>
                  <th className="th-lg">Exam</th>
                  <th className="th-lg">Proctor</th>
                  <th className="th-lg">Slots</th>
                  <th className="th-lg">Action</th>
                </tr>
              </thead>

              <tbody className="white">
                {schedules.map((schedule, index) => (
                  <RowAppointment
                    key={index}
                    schedule={schedule}
                    toggle={toggle}
                    index={index}
                  />
                ))}
              </tbody>
            </MDBTable>
            <div className="text-right">
              <small>(NOTE: You can only submit 1 appointment per exam.)</small>
            </div>
          </div>
        )}
      </MDBModalBody>
    </MDBModal>
  );
}
