import React, { useState, useEffect } from "react";
import {
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBSelect,
  MDBSelectInput,
  MDBSelectOptions,
  MDBSelectOption,
  MDBBadge,
  MDBView,
  MDBDatePicker,
} from "mdbreact";
import { Bar } from "react-chartjs-2";
import { useSelector } from "react-redux";
import { distinctFiltering } from "../../../../../services/utilities";
import { monthYearFormat } from "../../../../../services/utilities/timeAndDateFormat";

export default function UserStatistics() {
  const { persons } = useSelector(({ persons }) => persons);
  const [labels, setLabels] = useState([]);
  const [data, setData] = useState([]);
  const [dataRange, setDataRange] = useState("0");
  const [customDateRange, setCustomDateRange] = useState({
    from: null,
    to: null,
  });

  const handleCustomDateChange = (date, field) => {
    setCustomDateRange((prevRange) => ({
      ...prevRange,
      [field]: date,
    }));
  };

  useEffect(() => {
    if (customDateRange.from && customDateRange.to) {
      const filteredPersons = persons.filter((person) => {
        const createdAtDate = new Date(person.createdAt);
        return (
          createdAtDate >= customDateRange.from &&
          createdAtDate <= customDateRange.to
        );
      });

      const { labels, data } = distinctFiltering(filteredPersons, "createdAt");

      setData(data);
      setLabels(labels);
    }
  }, [customDateRange, persons]);

  useEffect(() => {
    const _dataRange = Number(dataRange);

    if (_dataRange > 0 && persons.length > 0) {
      let filteredPersons = [];

      switch (_dataRange) {
        case 1:
          filteredPersons = persons.filter(
            (person) =>
              new Date(person.createdAt).toLocaleDateString() ===
              new Date().toLocaleDateString()
          );
          break;

        case 3:
          filteredPersons = persons.filter((person) => {
            const createdAtDate = new Date(person.createdAt);
            const createdAtYear = createdAtDate.getFullYear();
            const currentYear = new Date().getFullYear();
            const lastYear = currentYear - 1;

            return (
              (createdAtYear === currentYear || createdAtYear === lastYear) &&
              createdAtDate.getMonth() >= 0 &&
              createdAtDate.getMonth() <= 11
            );
          });
          break;

        default:
          filteredPersons = persons;
          break;
      }

      const { labels, data } = distinctFiltering(
        filteredPersons,
        "createdAt",
        _dataRange === 3 ? monthYearFormat : null
      );

      setData(data);
      setLabels(labels);
    }
  }, [dataRange, persons]);

  useEffect(() => {
    if (persons.length > 0) {
      //   const customIdentifier = (email) => email; add customIdentifier as 3rd parameter below
      const { labels, data } = distinctFiltering(persons, "createdAt");

      setData(data);
      setLabels(labels);
    }
  }, [persons]);

  return (
    <section className="mb-5">
      <MDBCard cascade narrow>
        <MDBRow>
          <MDBCol xl="5" md="12" className="mr-0">
            <MDBView cascade className="gradient-card-header primary-color">
              <h4 className="h4-responsive mb-0 font-weight-bold">
                Registrations
              </h4>
            </MDBView>
            <MDBCardBody cascade className="pb-3">
              <MDBRow className="pt-3 card-body">
                <MDBCol md="12">
                  <h4>
                    <MDBBadge className="big-badge primary-color">
                      Data range
                    </MDBBadge>
                  </h4>
                  <MDBSelect getValue={(e) => setDataRange(e[0])}>
                    <MDBSelectInput selected="Choose time period" />
                    <MDBSelectOptions>
                      <MDBSelectOption disabled>
                        Choose time period
                      </MDBSelectOption>
                      <MDBSelectOption value="1">Today</MDBSelectOption>
                      <MDBSelectOption value="2">Daily</MDBSelectOption>
                      <MDBSelectOption value="3">Monthly</MDBSelectOption>
                    </MDBSelectOptions>
                  </MDBSelect>
                  <h5>
                    <MDBBadge className="big-badge primary-color">
                      Custom date
                    </MDBBadge>
                  </h5>
                  <br />
                  <div className="mb-1">
                    <MDBRow className="mb-3">
                      <MDBCol size="6">
                        <small className="grey-text">from:</small>
                        <MDBDatePicker
                          className="my-0 d-inline ml-3"
                          getValue={(date) =>
                            handleCustomDateChange(date, "from")
                          }
                        />
                      </MDBCol>
                      <MDBCol size="6">
                        <small className="grey-text">to:</small>
                        <MDBDatePicker
                          className="my-0 d-inline ml-3"
                          getValue={(date) =>
                            handleCustomDateChange(date, "to")
                          }
                        />
                      </MDBCol>
                    </MDBRow>
                  </div>
                </MDBCol>
              </MDBRow>
            </MDBCardBody>
          </MDBCol>
          <MDBCol md="12" xl="7">
            <MDBView cascade className="gradient-card-header primary-color">
              <Bar
                data={{
                  labels,
                  datasets: [
                    {
                      label: "# of Registers",
                      data,
                      backgroundColor: "rgba(255, 255, 255, 0.3)",
                      borderColor: "#fff",
                      borderWidth: 1,
                    },
                  ],
                }}
                options={{
                  responsive: true,
                  maintainAspectRatio: true,
                  legend: {
                    labels: {
                      fontColor: "white",
                      fontSize: 18,
                    },
                  },
                  scales: {
                    xAxes: [
                      {
                        barPercentage: 1,
                        gridLines: {
                          display: true,
                          color: "rgba(0, 0, 0, 0.1)",
                        },
                        ticks: {
                          fontColor: "#fff",
                        },
                      },
                    ],
                    yAxes: [
                      {
                        gridLines: {
                          display: true,
                          color: "rgba(0, 0, 0, 0.1)",
                        },
                        ticks: {
                          beginAtZero: true,
                          min: 0,
                          fontColor: "#fff",
                        },
                      },
                    ],
                  },
                }}
                height={160}
              />
            </MDBView>
          </MDBCol>
        </MDBRow>
      </MDBCard>
    </section>
  );
}
