import { MDBBtn } from "mdbreact";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import {
  BROWSE,
  SAVE,
} from "../../../../../services/redux/slices/appointments";
import {
  monthDayYearFormat,
  timeFormat,
} from "../../../../../services/utilities/timeAndDateFormat";

export default function RowAppointment({ schedule, toggle, index }) {
  const { token, auth } = useSelector(({ persons }) => persons);
  const { appointments } = useSelector(({ appointments }) => appointments);
  const dispatch = useDispatch();
  const [remainingSlots, setRemainingSlots] = useState(0);

  const [found, setFound] = useState([]);

  useEffect(() => {
    setRemainingSlots(schedule.maxSlots - schedule.slotsTaken);
  }, [schedule, remainingSlots]);

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  useEffect(() => {
    if (appointments) {
      setFound(
        appointments.find(
          (appointment) =>
            appointment?.schedule?._id === schedule._id &&
            appointment?.examinee?._id === auth._id
        )
      );
    }
  }, [appointments, schedule, auth]);

  const handleSubmit = (schedule) => {
    Swal.fire({
      icon: "info",
      html:
        "<h4 style='font-weight: bold;'>Are you sure?</h4>" +
        "<h6 style='font-weight: 400;'>You want to submit an appointment.</h6>",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        toggle();

        if (!found) {
          dispatch(
            SAVE({
              appointment: {
                examinee: auth._id,
                schedule: schedule._id,
              },
              token,
            })
          );
        }
      }
    });
  };
  return (
    <tr key={index}>
      <td>{index + 1}.</td>
      <td>{schedule.title}</td>
      <td>
        {monthDayYearFormat(schedule.start)} <br /> {timeFormat(schedule.start)}
      </td>
      <td>
        {monthDayYearFormat(schedule.end)} <br /> {timeFormat(schedule.end)}
      </td>
      <td>{schedule.exam?.title}</td>
      <td>{schedule.admin?.username}</td>
      <td>
        {remainingSlots}/{schedule.maxSlots}
      </td>
      <td>
        <MDBBtn
          disabled={remainingSlots === 0 || found !== undefined}
          color="success"
          size="sm"
          onClick={() => handleSubmit(schedule)}
        >
          Submit
        </MDBBtn>
      </td>
    </tr>
  );
}
