import React from "react";
import { MDBBtn, MDBIcon, MDBTooltip } from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import {
  SELECTED as SELECTED_APPOINTMENT,
  UPDATE,
} from "../../../../services/redux/slices/appointments";
import { useHistory } from "react-router-dom";
import {
  monthDayYearFormat,
  timeFormat,
} from "../../../../services/utilities/timeAndDateFormat";
import { SELECTEDEXAM } from "../../../../services/redux/slices/banks";
import { SELECTED as SELECTED_SCHEDULE } from "../../../../services/redux/slices/schedules";

export default function RowAppointment({ appointment, index, item }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleResubmit = () => {
    Swal.fire({
      icon: "info",
      html:
        "<h4 style='font-weight: bold;'>Are you sure?</h4>" +
        "<h6 style='font-weight: 400;'>You want to resubmit this appointment.</h6>",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(
          UPDATE({
            updateFields: {
              _id: appointment._id,
              status: "Pending",
              reason: "",
            },
            token,
          })
        );

        window.location.href = "/appointments";
      }
    });
  };

  const handleTakeExam = () => {
    if (
      new Date() >= new Date(appointment.schedule?.start) &&
      new Date() <= new Date(appointment.schedule?.end)
    ) {
      dispatch(SELECTED_APPOINTMENT(appointment));
      dispatch(SELECTEDEXAM(appointment.schedule?.exam));
      dispatch(SELECTED_SCHEDULE(appointment.schedule));
      history.push("/exam");
    } else {
      Swal.fire({
        icon: "info",
        title: "Exam Not Available",
        text: "Please check back on the exact exam day/time.",
        confirmButtonText: "OK",
      });
    }
  };

  return (
    <tr>
      <th>{index + 1}.</th>
      <td>{appointment.schedule?.title}</td>
      <td>
        {monthDayYearFormat(appointment.schedule?.start)} <br />
        {timeFormat(appointment.schedule?.start)}
      </td>
      <td>
        {monthDayYearFormat(appointment.schedule?.end)} <br />
        {timeFormat(appointment.schedule?.end)}
      </td>
      <td>{appointment.schedule?.exam?.title}</td>
      <td>{appointment.schedule?.admin?.username}</td>
      {item.value === "Denied" && (
        <>
          <td>{appointment.reason}</td>
          <td>
            <MDBBtn color="primary" onClick={handleResubmit} size="sm">
              Resubmit
            </MDBBtn>
          </td>
        </>
      )}
      {item.value === "Approved" && (
        <td>
          <MDBTooltip placement="right">
            <MDBBtn
              outline
              color="blue"
              rounded
              size="sm"
              className="px-2"
              onClick={handleTakeExam}
            >
              <MDBIcon icon="arrow-right" className="mt-0" />
            </MDBBtn>
            <div>Take Exam</div>
          </MDBTooltip>
        </td>
      )}
    </tr>
  );
}
