import React, { useEffect, useState } from "react";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBCard,
  MDBBtn,
  MDBIcon,
} from "mdbreact";
import UserStatistics from "./statistics/users";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE as PERSONS_BROWSE } from "../../../../services/redux/slices/persons";
import { BROWSE as EXAMS_BROWSE } from "../../../../services/redux/slices/exams";
import {
  BROWSE as SCORES_BROWSE,
  STATISTICS,
} from "../../../../services/redux/slices/scores";
import PassingPercentages from "./statistics/passingPercentages";
import { getLastMonthRange } from "../../../../services/utilities/getLastMonthRange";

export default function Dashboard() {
  const { token, persons } = useSelector(({ persons }) => persons);
  const { exams } = useSelector(({ exams }) => exams);
  const { scores, examPassers } = useSelector(({ scores }) => scores);
  const [registeredUsers, setRegisteredUsers] = useState([]);

  const [statisticsLastMonth, setStatisticsLastMonth] = useState({
    availableExams: [],
    registeredUsers: [],
    examTakers: [],
    examPassers: [],
  });

  const dispatch = useDispatch();
  useEffect(() => {
    if (token) {
      dispatch(PERSONS_BROWSE(token));
      dispatch(EXAMS_BROWSE(token));
      dispatch(SCORES_BROWSE(token));
    }
  }, [token, dispatch]);

  useEffect(() => {
    if (token) {
      dispatch(STATISTICS(token));
    }
  }, [token, dispatch]);

  useEffect(() => {
    const { start, end } = getLastMonthRange();

    setStatisticsLastMonth({
      availableExams: exams.filter((exam) => {
        const createdAtDate = new Date(exam.createdAt);
        return createdAtDate >= start && createdAtDate <= end;
      }),
      registeredUsers: registeredUsers.filter((person) => {
        const createdAtDate = new Date(person.createdAt);
        return createdAtDate >= start && createdAtDate <= end;
      }),
      examTakers: scores.filter((score) => {
        const createdAtDate = new Date(score.createdAt);
        return createdAtDate >= start && createdAtDate <= end;
      }),
      examPassers: examPassers.filter((person) => {
        const createdAtDate = new Date(person.createdAt);
        return createdAtDate >= start && createdAtDate <= end;
      }),
    });
  }, [exams, persons, examPassers, scores, registeredUsers]);

  useEffect(() => {
    setRegisteredUsers(
      persons.filter(
        (person) => person.role === "EXAMINEE" || person.role === "RECEPTIONIST"
      )
    );
  }, [persons]);
  return (
    <MDBContainer fluid id="v6" className="mb-5">
      <section className="mb-4">
        <MDBRow>
          <MDBCol xl="3" md="6" className="mb-4 mb-r">
            <MDBCard>
              <MDBRow className="mt-3">
                <MDBCol md="5" col="5" className="text-left pl-4">
                  <MDBBtn
                    tag="a"
                    floating
                    size="lg"
                    color="info"
                    className="ml-4"
                    style={{ padding: 0 }}
                  >
                    <MDBIcon icon="user" size="2x" />
                  </MDBBtn>
                </MDBCol>
                <MDBCol md="7" col="7" className="text-right pr-5">
                  <h5 className="ml-4 mt-4 mb-2 font-weight-bold">
                    {registeredUsers.length}
                  </h5>
                  <p className="font-small grey-text">Registered Users</p>
                </MDBCol>
              </MDBRow>
              <MDBRow className="my-3">
                <MDBCol md="7" col="7" className="text-left pl-4">
                  <p className="font-small dark-grey-text font-up ml-4 font-weight-bold">
                    Last Month
                  </p>
                </MDBCol>
                <MDBCol md="5" col="5" className="text-right pr-5">
                  <p className="font-small grey-text">
                    {statisticsLastMonth.registeredUsers.length}
                  </p>
                </MDBCol>
              </MDBRow>
            </MDBCard>
          </MDBCol>

          <MDBCol xl="3" md="6" className="mb-4 mb-r">
            <MDBCard>
              <MDBRow className="mt-3">
                <MDBCol md="5" size="5" className="text-left pl-4">
                  <MDBBtn
                    tag="a"
                    floating
                    size="lg"
                    color="primary"
                    className="ml-4"
                    style={{ padding: 0 }}
                  >
                    <MDBIcon icon="book-open" size="2x" />
                  </MDBBtn>
                </MDBCol>
                <MDBCol md="7" col="7" className="text-right pr-5">
                  <h5 className="ml-4 mt-4 mb-2 font-weight-bold">
                    {exams.length}
                  </h5>
                  <p className="font-small grey-text">Available Exams</p>
                </MDBCol>
              </MDBRow>
              <MDBRow className="my-3">
                <MDBCol md="7" col="7" className="text-left pl-4">
                  <p className="font-small dark-grey-text font-up ml-4 font-weight-bold">
                    Last Month
                  </p>
                </MDBCol>
                <MDBCol md="5" col="5" className="text-right pr-5">
                  <p className="font-small grey-text">
                    {statisticsLastMonth.availableExams.length}
                  </p>
                </MDBCol>
              </MDBRow>
            </MDBCard>
          </MDBCol>

          <MDBCol xl="3" md="6" className="mb-4 mb-r">
            <MDBCard>
              <MDBRow className="mt-3">
                <MDBCol md="5" col="5" className="text-left pl-4">
                  <MDBBtn
                    tag="a"
                    floating
                    size="lg"
                    color="warning"
                    className="ml-4"
                    style={{ padding: 0 }}
                  >
                    <MDBIcon icon="user-edit" size="2x" />
                  </MDBBtn>
                </MDBCol>
                <MDBCol md="7" col="7" className="text-right pr-5">
                  <h5 className="ml-4 mt-4 mb-2 font-weight-bold">
                    {scores.length}
                  </h5>
                  <p className="font-small grey-text">Exams Takers</p>
                </MDBCol>
              </MDBRow>
              <MDBRow className="my-3">
                <MDBCol md="7" col="7" className="text-left pl-4">
                  <p className="font-small dark-grey-text font-up ml-4 font-weight-bold">
                    Last Month
                  </p>
                </MDBCol>
                <MDBCol md="5" col="5" className="text-right pr-5">
                  <p className="font-small grey-text">
                    {statisticsLastMonth.examTakers.length}
                  </p>
                </MDBCol>
              </MDBRow>
            </MDBCard>
          </MDBCol>

          <MDBCol xl="3" md="6" className="mb-4 mb-r">
            <MDBCard>
              <MDBRow className="mt-3">
                <MDBCol md="5" col="5" className="text-left pl-4">
                  <MDBBtn
                    tag="a"
                    floating
                    size="lg"
                    color="success"
                    className="ml-4"
                    style={{ padding: 0 }}
                  >
                    <MDBIcon icon="check" size="2x" />
                  </MDBBtn>
                </MDBCol>
                <MDBCol md="7" col="7" className="text-right pr-5">
                  <h5 className="ml-4 mt-4 mb-2 font-weight-bold">
                    {examPassers.length}
                  </h5>
                  <p className="font-small grey-text">Exam Passers</p>
                </MDBCol>
              </MDBRow>
              <MDBRow className="my-3">
                <MDBCol md="7" col="7" className="text-left pl-4">
                  <p className="font-small dark-grey-text font-up ml-4 font-weight-bold">
                    Last Month
                  </p>
                </MDBCol>
                <MDBCol md="5" col="5" className="text-right pr-5">
                  <p className="font-small grey-text">
                    {statisticsLastMonth.examPassers.length}
                  </p>
                </MDBCol>
              </MDBRow>
            </MDBCard>
          </MDBCol>
        </MDBRow>
      </section>
      <UserStatistics />
      <PassingPercentages />
    </MDBContainer>
  );
}
