const locations = (location) => {
  switch (location) {
    case "/dashboard":
      return "Dashboard";

    case "/exams":
      return "Exams";

    case "/banks":
      return "Banks";

    case "/persons":
      return "Persons";

    case "/scores":
      return "Scores";

    case "/appointments":
      return "Appointments";

    case "/schedules":
      return "Schedules";

    case "/requests":
      return "Pending Requests";

    default:
      return "Looks like you're lost.";
  }
};

export default locations;
