import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { axioKit, socket } from "../../utilities";

const name = "persons";

const initialState = {
  persons: [],
  isSuccess: false,
  isLoading: false,
  message: "",
  _id: "",
  selectedPerson: {},
  index: -1,
  auth: JSON.parse(localStorage.getItem("auth")) || null,
  token: localStorage.getItem("token") || "",
};

export const LOGIN = createAsyncThunk(`${name}/login`, (form, thunkAPI) => {
  try {
    return axioKit.login(name, form.email, form.password);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const BROWSE = createAsyncThunk(`${name}`, (token, thunkAPI) => {
  try {
    return axioKit.browse(name, token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const SAVE = createAsyncThunk(`${name}/save`, (person, thunkAPI) => {
  try {
    return axioKit.save(name, person);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const DELETE = createAsyncThunk(
  `${name}/id/destroy`,
  (form, thunkAPI) => {
    try {
      return axioKit.destroy(name, form.id, form.token);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);
export const PROMOTE_DEMOTE = createAsyncThunk(
  `${name}/update`,
  (form, thunkAPI) => {
    try {
      return axioKit.update(name, form.updatedRole, form.token);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.isSuccess = false;
      state.message = "";
    },
    SETID: (state, data) => {
      state._id = data.payload;
    },
    SELECTED: (state, data) => {
      state.selectedPerson = data.payload;
      const index = state.persons.findIndex(
        (person) => person._id === state.selectedPerson._id
      );
      state.index = index;
    },
    UPDATEINDEX: (state, data) => {
      state.index = data.payload;
    },
    INJECTPERSON: (state, data) => {
      state.persons.unshift(data.payload);
    },
    DELETEPERSON: (state, data) => {
      state.persons.splice(data.payload, 1);
    },
    UPDATEPERSON: (state, data) => {
      const { index, person } = data.payload;
      state.persons[index] = person;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(LOGIN.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(LOGIN.fulfilled, (state, action) => {
        const { auth, token } = action.payload;
        state.auth = auth;
        state.token = token;
        localStorage.setItem("auth", JSON.stringify(auth)); // Local storage can have an [] & {} in its 2nd parameter. So stringify converts it to string
        localStorage.setItem("token", token); //save to local storage cause once the redux is refresh the item value will be gone
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(LOGIN.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        alert(error.message);
        state.isLoading = false;
      })

      .addCase(BROWSE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(BROWSE.fulfilled, (state, action) => {
        state.persons = action.payload;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(BROWSE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        state.persons.unshift(action.payload);
        socket.emit("add_person", action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(DELETE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(DELETE.fulfilled, (state, action) => {
        const index = state.persons.findIndex(
          (person) => person._id === state._id
        );
        socket.emit("delete_person", index);
        state.persons.splice(index, 1);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(DELETE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(PROMOTE_DEMOTE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(PROMOTE_DEMOTE.fulfilled, (state, action) => {
        const index = state.persons.findIndex(
          (person) => person._id === action.payload._id
        );
        state.persons[index] = action.payload;
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(PROMOTE_DEMOTE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const {
  RESET,
  SETID,
  SELECTED,
  UPDATEINDEX,
  INJECTPERSON,
  DELETEPERSON,
  UPDATEPERSON,
} = reduxSlice.actions;

export default reduxSlice.reducer;
