import {
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE } from "../../../../services/redux/slices/appointments";
import RowAppointment from "./row";

export default function Requests() {
  const { appointments } = useSelector(({ appointments }) => appointments);
  const { token } = useSelector(({ persons }) => persons);
  const [pendingAppointments, setPendingAppointments] = useState([]);

  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  useEffect(() => {
    if (appointments) {
      setPendingAppointments(
        appointments.filter((appointment) => appointment.status === "Pending")
      );
    }
  }, [appointments]);

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-3 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            Pending Appointments
          </a>
        </MDBView>
        <MDBCardBody>
          {pendingAppointments.length === 0 ? (
            <div className="text-center">No Pending Requests Yet..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th className="th-lg">Examinee</th>
                  <th className="th-lg">Schedule</th>
                  <th className="th-lg">Exam</th>
                  <th className="th-lg">Proctor</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {pendingAppointments.map((appointment, index) => (
                  <RowAppointment key={index} appointment={appointment} />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
