import { MDBBtn, MDBContainer, MDBTypography } from "mdbreact";
import React from "react";
import { useState } from "react";
import { socket } from "../../services/utilities";
import { useEffect } from "react";

export default function Socket() {
  const [clicks, setClicks] = useState(0);

  const handleClicks = () => {
    const _clicks = clicks + 1;
    setClicks(_clicks);
    socket.emit("send_click", _clicks);
  };

  useEffect(() => {
    socket.on("recieve_click", setClicks);

    return () => socket.off("recieve_click");
  }, []);

  return (
    <MDBContainer>
      <MDBBtn onClick={handleClicks}>Click Here</MDBBtn>
      <MDBTypography tag="h1">{clicks}</MDBTypography>
    </MDBContainer>
  );
}
