import React, { useState, useEffect } from "react";
import {
  MDBSideNavLink,
  MDBSideNavCat,
  MDBSideNavNav,
  MDBSideNav,
  MDBIcon,
} from "mdbreact";
import ADMINISTRATOR from "../../pages/platforms/administrator/sidebar";
import EXAMINEE from "../../pages/platforms/examinee/sidebar";
import RECEPTIONIST from "../../pages/platforms/receptionist/sidebar";
import { useSelector } from "react-redux";

export default function SideNavigation({
  triggerOpening,
  breakWidth,
  onLinkClick,
}) {
  const [links, setLinks] = useState([]);
  const { auth } = useSelector(({ persons }) => persons);

  useEffect(() => {
    if (auth.role) {
      const collection = {
        ADMINISTRATOR,
        EXAMINEE,
        RECEPTIONIST,
      };
      setLinks(collection[auth.role] || []);
    }
  }, [auth]);

  const handleLinks = () => {
    if (links.length > 0) {
      return links?.map(({ path, name, icon, children }, index) =>
        path ? (
          <MDBSideNavLink
            key={`sidebar-${index}`}
            to={path}
            topLevel
            onClick={onLinkClick}
          >
            <MDBIcon icon={`${icon} mr-2`} />
            {name}
          </MDBSideNavLink>
        ) : (
          <MDBSideNavCat
            id={`${name}-cat`}
            name={name}
            key={`sidebar-${index}`}
            icon={icon}
          >
            {children.map((child, cIndex) => (
              <MDBSideNavLink
                key={`sidebar-${index}-${cIndex}`}
                to={child.path}
                onClick={onLinkClick}
              >
                {child.name}
              </MDBSideNavLink>
            ))}
          </MDBSideNavCat>
        )
      );
    } else {
      return (
        <MDBSideNavLink to="/" topLevel onClick={onLinkClick}>
          <MDBIcon icon="home mr-2" />
          Home
        </MDBSideNavLink>
      );
    }
  };

  return (
    <div className="white-skin">
      <MDBSideNav
        logo="https://mdbootstrap.com/img/Marketing/general/logo/medium/mdb-react.png"
        bg="https://mdbootstrap.com/img/Photos/Others/sidenav2.jpg"
        mask="strong"
        href="/dashboard"
        fixed
        breakWidth={breakWidth}
        triggerOpening={triggerOpening}
        style={{ transition: "padding-left .3s" }}
      >
        <MDBSideNavNav>{handleLinks()}</MDBSideNavNav>
      </MDBSideNav>
    </div>
  );
}
