import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import { axioKit, socket } from "../../utilities";

const name = "scores";

const initialState = {
  scores: [],
  isSuccess: false,
  isLoading: false,
  hasUpdated: false,
  message: "",
  _id: "",
  selectedScore: {},
  index: -1,
  query: JSON.parse(localStorage.getItem("query")) || null,
  passingPercentages: [],
  examPassers: [],
};

export const STATISTICS = createAsyncThunk(
  `${name}/statistics`,
  (token, thunkAPI) => {
    try {
      return axios
        .get("scores/statistics", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        })
        .then(({ data }) => {
          const { passedExaminees, payload } = data;
          return { passedExaminees, payload };
        })
        .catch(({ response }) => {
          const { error } = response.data;
          throw new Error(error);
        });
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

export const BROWSE = createAsyncThunk(`${name}`, (token, thunkAPI) => {
  try {
    return axioKit.browse(name, token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const SAVE = createAsyncThunk(`${name}/save`, (form, thunkAPI) => {
  try {
    return axioKit.save(name, form.score, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.isSuccess = false;
      state.message = "";
      state.hasUpdated = false;
    },
    SETID: (state, data) => {
      state._id = data.payload;
    },
    SELECTED: (state, data) => {
      state.selectedScore = data.payload;
      const index = state.scores.findIndex(
        (score) => score._id === state.selectedScore._id
      );
      state.index = index;
    },
    SETQUERY: (state, data) => {
      state.query = data.payload;
      localStorage.setItem("query", JSON.stringify(data.payload));
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(BROWSE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(BROWSE.fulfilled, (state, action) => {
        state.scores = action.payload;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(BROWSE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        state.scores.unshift(action.payload);
        socket.emit("add_score", action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(STATISTICS.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(STATISTICS.fulfilled, (state, action) => {
        const { passedExaminees, payload } = action.payload;

        state.passingPercentages = payload;
        state.examPassers = passedExaminees;

        state.isSuccess = true;
        state.isLoading = false;
        state.hasUpdated = true;
      })

      .addCase(STATISTICS.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const { RESET, SETID, SELECTED, SETQUERY } = reduxSlice.actions;

export default reduxSlice.reducer;
