import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { socket } from "../../utilities";
import { axioKit } from "../../utilities";

const name = "banks";

const initialState = {
  banks: [],
  isSuccess: false,
  isLoading: false,
  message: "",
  _id: "",
  selectedBank: {},
  selectedExam: JSON.parse(localStorage.getItem("selectedExam")) || null,
  index: -1,
};

export const SAVE = createAsyncThunk(`${name}/save`, (form, thunkAPI) => {
  try {
    return axioKit.save(name, form.bank, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const DELETE = createAsyncThunk(
  `${name}/id/destroy`,
  (form, thunkAPI) => {
    try {
      return axioKit.destroy(name, form.id, form.token);
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

export const UPDATE = createAsyncThunk(`${name}/update`, (form, thunkAPI) => {
  try {
    return axioKit.update(name, form.bank, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.isSuccess = false;
      state.message = "";
    },
    SETID: (state, data) => {
      state._id = data.payload;
    },
    SELECTED: (state, data) => {
      state.selectedBank = data.payload;

      state.index = state.banks.findIndex(
        (bank) => bank._id === data.payload._id
      );
    },
    UPDATEINDEX: (state, data) => {
      state.index = data.payload;
    },
    SELECTEDEXAM: (state, data) => {
      state.selectedExam = data.payload;
      localStorage.setItem("selectedExam", JSON.stringify(data.payload));
    },
  },
  extraReducers: (builder) => {
    builder

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        state.banks.unshift(action.payload);
        socket.emit("add_bank", action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(DELETE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(DELETE.fulfilled, (state, action) => {
        const index = state.banks.findIndex((bank) => bank._id === state._id);

        state.banks.splice(index, 1);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(DELETE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(UPDATE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(UPDATE.fulfilled, (state, action) => {
        const index = state.banks.findIndex(
          (bank) => bank._id === action.payload._id
        );
        state.banks[index] = action.payload;
        socket.emit("update_bank", { index, bank: action.payload });
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(UPDATE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const { RESET, SETID, SELECTED, UPDATEINDEX, SELECTEDEXAM } =
  reduxSlice.actions;

export default reduxSlice.reducer;
