import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import { axioKit } from "../../utilities";

const name = "exams";

const initialState = {
  exams: [],
  isSuccess: false,
  isLoading: false,
  message: "",
  _id: "",
  selectedExam: {},
  index: -1,
};

export const BROWSE = createAsyncThunk(`${name}`, (token, thunkAPI) => {
  try {
    return axioKit.browse(name, token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const SAVE = createAsyncThunk(`${name}/save`, (form, thunkAPI) => {
  try {
    return axioKit.save(name, form.exam, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const DELETE = createAsyncThunk(
  `${name}/id/destroy`,
  (form, thunkAPI) => {
    try {
      return axios
        .delete(`exams/${form.id}/destroy`, {
          headers: {
            Authorization: `Bearer ${form.token}`,
          },
        })
        .then(() => {
          return true;
        })
        .catch((err) => {
          throw new Error(err);
        });
    } catch (error) {
      const message =
        (error.response &&
          error.response.data &&
          error.response.data.message) ||
        error.message ||
        error.toString();

      return thunkAPI.rejectWithValue(message);
    }
  }
);

export const UPDATE = createAsyncThunk(`${name}/update`, (form, thunkAPI) => {
  try {
    return axios
      .put("exams/update", form.exam, {
        headers: {
          Authorization: `Bearer ${form.token}`,
        },
      })
      .then(({ data }) => {
        const { payload } = data;
        return payload;
      })
      .catch((err) => {
        throw new Error(err);
      });
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.isSuccess = false;
      state.message = "";
    },
    SETID: (state, data) => {
      state._id = data.payload;
    },
    SELECTED: (state, data) => {
      state.selectedExam = data.payload;
      const index = state.exams.findIndex(
        (exam) => exam._id === state.selectedExam._id
      );
      state.index = index;
    },
    UPDATEINDEX: (state, data) => {
      state.index = data.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(BROWSE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(BROWSE.fulfilled, (state, action) => {
        state.exams = action.payload;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(BROWSE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        state.exams.unshift(action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(DELETE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(DELETE.fulfilled, (state, action) => {
        const index = state.exams.findIndex((exam) => exam._id === state._id);

        state.exams.splice(index, 1);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(DELETE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(UPDATE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(UPDATE.fulfilled, (state, action) => {
        const index = state.exams.findIndex(
          (exam) => exam._id === action.payload._id
        );
        state.exams[index] = action.payload;
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(UPDATE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const { RESET, SETID, SELECTED, UPDATEINDEX } = reduxSlice.actions;

export default reduxSlice.reducer;
