const distinctFiltering = (array = [], key = "", customFormat = null) => {
  var format = (date) => new Date(date).toLocaleDateString();

  if (customFormat) {
    format = customFormat;
  }

  const labels = Array.from(new Set(array.map((item) => format(item[key]))));

  const data = labels.map(
    (label) => array.filter((item) => format(item[key]) === label).length
  );

  return { labels, data };
};

export default distinctFiltering;
