import Dashboard from "./dashboard";
import Exams from "./exams";
import Exam from "./exam";
import Scores from "../administrator/scores";
import Appointments from "./appointments";

const routes = [
  {
    path: "/dashboard",
    component: Dashboard,
  },
  {
    path: "/exams",
    component: Exams,
  },
  {
    path: "/exam",
    component: Exam,
  },
  {
    path: "/scores",
    component: Scores,
  },
  {
    path: "/appointments",
    component: Appointments,
  },
];

export default routes;
