import {
  MDBBtn,
  MDBBtnGroup,
  MDBContainer,
  MDBIcon,
  MDBInput,
  MDBInputGroup,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  toast,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  SAVE,
  UPDATE,
  UPDATEINDEX,
} from "../../../../services/redux/slices/banks";
import { BROWSE } from "../../../../services/redux/slices/exams";

export default function CreateBank({ modal, toggle }) {
  const dispatch = useDispatch();
  const { isLoading, index, selectedBank, selectedExam } = useSelector(
    ({ banks }) => banks
  );

  const { token } = useSelector(({ persons }) => persons);
  const [willUpdate, setWillUpdate] = useState(false);

  useEffect(() => {
    if (index >= 0) {
      setWillUpdate(true);
    }
  }, [index]);

  useEffect(() => {
    dispatch(BROWSE(token));
  }, [dispatch, token]);

  const [bank, setBank] = useState({
    exam: selectedExam?._id,
    question: "",
    choices: ["", ""],
    answer: 0,
  });

  useEffect(() => {
    if (index >= 0) {
      setBank(selectedBank);
    }
  }, [index, selectedBank]);

  const handleChange = (key, value, index) => {
    if (key === "choices") {
      const updatedChoices = [...bank.choices];
      updatedChoices[index] = value;
      setBank({
        ...bank,
        choices: updatedChoices,
      });
    } else {
      setBank({
        ...bank,
        [key]: value,
      });
    }
  };

  const handleAnswer = (answerIndex) => {
    setBank((prevBank) => ({ ...prevBank, answer: answerIndex }));
  };

  const handleAddChoice = () => {
    if (bank.choices?.length === 5) return toast.warn("Maximum of 5 choices");

    setBank({
      ...bank,
      choices: [...bank.choices, ""],
    });
  };

  const handleRemoveChoice = (index) => {
    const updatedChoices = [...bank.choices];

    if (updatedChoices.length < 3) return toast.warn("Minimum of 2 choices");

    updatedChoices.splice(index, 1);
    setBank({
      ...bank,
      choices: updatedChoices,
    });
  };

  const handleReset = () => {
    setBank({
      exam: selectedExam._id,
      question: "",
      choices: ["", ""],
      answer: 0,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (willUpdate) {
      dispatch(UPDATE({ bank, token }));
      handleReset();
      dispatch(UPDATEINDEX(-1));
    } else {
      if (bank.choices.includes(""))
        return toast.error("All choices fields are required");

      dispatch(SAVE({ bank, token }));
      handleReset();
    }
    toggle();
    window.location.href = "/exams/banks";
  };

  const toggleModal = () => {
    handleReset();
    dispatch(UPDATEINDEX(-1));
    toggle();
  };
  return (
    <MDBModal
      size="md"
      className={`modal-notify modal-${willUpdate ? "warning" : "success"}`}
      backdrop={true}
      isOpen={modal}
      toggle={toggleModal}
    >
      <MDBModalHeader tag="p" toggle={toggleModal} titleClass="heading lead">
        {willUpdate ? "Update Bank" : "Create Bank"}
      </MDBModalHeader>

      <MDBModalBody>
        <form onSubmit={handleSubmit}>
          <MDBInput
            type="text"
            label="Question"
            value={bank.question}
            onChange={(e) => handleChange("question", e.target.value)}
            required
          />

          <div className="d-flex justify-content-between align-items-center">
            <div>Choices:</div>

            <MDBBtn
              color="primary"
              size="sm"
              className="px-3"
              onClick={handleAddChoice}
            >
              <MDBIcon icon="plus" className="mt-0" />
            </MDBBtn>
          </div>

          {bank.choices.map((choice, index) => (
            <MDBInputGroup
              key={index}
              type="text"
              value={choice}
              onChange={(e) => handleChange("choices", e.target.value, index)}
              required
              material
              prepend={
                <div className="d-flex align-items-center">
                  {String.fromCharCode(65 + index)}.
                </div>
              }
              append={
                <MDBBtn
                  color="danger"
                  size="sm"
                  className="px-3"
                  onClick={() => handleRemoveChoice(index)}
                >
                  <MDBIcon icon="minus" className="mt-0" />
                </MDBBtn>
              }
            />
          ))}

          <div className="text-left">
            <div>Answer:</div>
            <MDBBtnGroup className="w-100">
              {bank.choices.map((choice, answerIndex) => (
                <MDBBtn
                  key={answerIndex}
                  outline={answerIndex !== bank.answer}
                  color="info"
                  onClick={() => handleAnswer(answerIndex)}
                >
                  {String.fromCharCode(65 + answerIndex)}.
                </MDBBtn>
              ))}
            </MDBBtnGroup>
          </div>

          <MDBContainer className="text-center">
            <MDBBtn
              disabled={isLoading}
              type="submit"
              color={`${willUpdate ? "warning" : "success"}`}
              className="w-40"
            >
              {willUpdate ? "Update" : "Submit"}
            </MDBBtn>
          </MDBContainer>
        </form>
      </MDBModalBody>
    </MDBModal>
  );
}
