import { MDBCard, MDBView } from "mdbreact";
import { Line } from "react-chartjs-2";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import find from "../../../../../services/utilities/axioKit/find";

export default function PassingPercentages() {
  const { token, auth } = useSelector(({ persons }) => persons);
  const [labels, setLabels] = useState([]);
  const [data, setData] = useState([]);

  const [examineePassingPercentages, setExamineePassingPercentages] = useState(
    []
  );
  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      const handleStatistics = async () => {
        setExamineePassingPercentages(
          await find(
            "scores",
            "examinee_statistics",
            "examinee",
            auth?._id,
            token
          )
        );
      };

      handleStatistics();
    }
  }, [token, dispatch, auth]);

  useEffect(() => {
    if (examineePassingPercentages) {
      const chartLabels = examineePassingPercentages.map(
        (passingPercentage) => passingPercentage.exam
      );
      const chartData = examineePassingPercentages.map(
        (passingPercentage) => passingPercentage.percentage
      );
      setLabels(chartLabels);
      setData(chartData);
    }
  }, [examineePassingPercentages]);

  return (
    <section className="mb-5 pt-5">
      <MDBCard narrow className="mb-5">
        <MDBView cascade className="gradient-card-header blue-gradient mb-4">
          <Line
            data={{
              labels,
              datasets: [
                {
                  label: "% of Passing Per Exam Taken",
                  data,
                  fillColor: "rgba(220,220,220,0.2)",
                  strokeColor: "rgba(220,220,220,1)",
                  pointColor: "rgba(220,220,220,1)",
                  pointStrokeColor: "#fff",
                  pointHighlightFill: "#fff",
                  pointHighlightStroke: "rgba(220,220,220,1)",
                  backgroundColor: "rgba(255, 255, 255, 0.2)",
                  borderColor: "#fff",
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              responsive: true,
              maintainAspectRatio: true,
              legend: {
                labels: {
                  fontColor: "white",
                  fontSize: 16,
                },
              },
              scales: {
                xAxes: [
                  {
                    gridLines: {
                      display: true,
                      color: "rgba(255, 255, 255, 0.2)",
                    },
                    ticks: {
                      fontColor: "#fff",
                    },
                  },
                ],
                yAxes: [
                  {
                    gridLines: {
                      display: true,
                      color: "rgba(255, 255, 255, 0.2)",
                    },
                    ticks: {
                      fontColor: "#fff",
                      callback: function (value) {
                        return value + "%"; // Add the percentage symbol
                      },
                    },
                  },
                ],
              },
            }}
            height={200}
          />
        </MDBView>
      </MDBCard>
    </section>
  );
}
