import { MDBBtn, MDBBtnGroup } from "mdbreact";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import { SELECTEDEXAM } from "../../../../services/redux/slices/banks";
import {
  DELETE,
  SELECTED,
  SETID,
} from "../../../../services/redux/slices/exams";
import { SETQUERY } from "../../../../services/redux/slices/scores";

export default function RowExam({ exam, toggle }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleDelete = () => {
    Swal.fire({
      icon: "question",
      title: "Are you sure?",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(SETID(exam._id));
        dispatch(DELETE({ id: exam._id, token }));
      }
    });
  };

  const handleUpdate = () => {
    toggle();
    dispatch(SELECTED(exam));
  };

  const handleBanks = () => {
    dispatch(SELECTEDEXAM(exam));
    history.push("/exams/banks");
  };

  const handleScores = () => {
    history.push("/scores");
    dispatch(SETQUERY({ key: "exam", value: exam }));
  };

  return (
    <tr>
      <td>{exam.title}</td>
      <td>{exam.description}</td>
      <td>
        <MDBBtnGroup>
          <MDBBtn color="warning" size="sm" onClick={handleUpdate}>
            Update
          </MDBBtn>
          <MDBBtn color="danger" size="sm" onClick={handleDelete}>
            Delete
          </MDBBtn>
          <MDBBtn color="primary" size="sm" onClick={handleBanks}>
            Banks
          </MDBBtn>
          <MDBBtn color="info" size="sm" onClick={handleScores}>
            Scores
          </MDBBtn>
        </MDBBtnGroup>
      </td>
    </tr>
  );
}
