import { MDBBtn, MDBBtnGroup } from "mdbreact";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  DELETE,
  PROMOTE_DEMOTE,
  SETID,
} from "../../../../services/redux/slices/persons";
import Swal from "sweetalert2";
import { useHistory } from "react-router";
import { SETQUERY } from "../../../../services/redux/slices/scores";
import { monthDayYearFormat } from "../../../../services/utilities/timeAndDateFormat";

export default function RowPerson({ person, index }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleDelete = () => {
    Swal.fire({
      icon: "question",
      title: "Are you sure?",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(SETID(person._id));
        dispatch(DELETE({ id: person._id, token }));
      }
    });
  };

  const handleScores = () => {
    history.push("/scores");
    dispatch(SETQUERY({ key: "examinee", value: person }));
  };

  const handlePromotion = () => {
    dispatch(
      PROMOTE_DEMOTE({
        updatedRole: {
          _id: person._id,
          role: person.role === "EXAMINEE" ? "RECEPTIONIST" : "EXAMINEE",
        },
        token,
      })
    );
  };

  return (
    <tr>
      <th>{index + 1}.</th>
      <td>{person.username}</td>
      <td>{person.email}</td>
      <td>{monthDayYearFormat(person.dob)}</td>
      <td>
        <MDBBtnGroup>
          <MDBBtn color="danger" size="sm" onClick={handleDelete}>
            Delete
          </MDBBtn>
          {person.role !== "ADMINISTRATOR" && (
            <>
              {person.role !== "RECEPTIONIST" && (
                <MDBBtn color="info" size="sm" onClick={handleScores}>
                  Scores
                </MDBBtn>
              )}

              <MDBBtn
                color={`${person.role === "EXAMINEE" ? "success" : "warning"}`}
                size="sm"
                onClick={handlePromotion}
              >
                {person.role === "EXAMINEE" ? "Promote" : "Demote"}
              </MDBBtn>
            </>
          )}
        </MDBBtnGroup>
      </td>
    </tr>
  );
}
