import React from "react";
import {
  MDBBtn,
  MDBContainer,
  MDBInput,
  MDBCard,
  MDBCardBody,
  MDBView,
  MDBMask,
  MDBRow,
  MDBCol,
  MDBIcon,
} from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { LOGIN } from "../../services/redux/slices/persons";
import { useEffect } from "react";
import { useHistory } from "react-router";

export default function Home() {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleSubmit = (e) => {
    e.preventDefault();
    const { email, password } = e.target;
    dispatch(LOGIN({ email: email.value, password: password.value }));
  };

  useEffect(() => {
    if (token) history.push("/dashboard"); // in redux 'history' & 'location' redirect without refreshing unlike window.location.href in js
  }, [token, history]);

  return (
    <section id="home">
      <MDBView>
        <MDBMask
          className="d-flex justify-content-center align-items-center"
          overlay="gradient"
        >
          <MDBContainer className="h-100 d-flex justify-content-center align-items-center">
            <MDBRow className="flex-center ">
              <MDBCol md="6" className="text-center text-md-left mb-5">
                <div className="white-text">
                  <h1
                    className="h1-responsive font-weight-bold wow fadeInLeft"
                    data-wow-delay="0.3s"
                  >
                    Learn with us right now!{" "}
                  </h1>
                  <hr
                    className="hr-light wow fadeInLeft"
                    data-wow-delay="0.3s"
                  />
                  <h6 className="wow fadeInLeft" data-wow-delay="0.3s">
                    Welcome to our state-of-the-art Examination System, where
                    education meets innovation! We take pride in delivering a
                    seamless and intuitive experience.
                  </h6>
                  <br />
                  <h6 className="wow fadeInLeft" data-wow-delay="0.3s">
                    Embrace the power of ourselves to unlock your potential and
                    pave the way for a brighter future. Let's embark together on
                    the path of continuous learning and success!
                  </h6>
                </div>
              </MDBCol>

              <MDBCol md="6" className="col-xl-5 offset-xl-1">
                <MDBCard>
                  <MDBCardBody>
                    <div className="text-center">
                      <h3 className="white-text">
                        <MDBIcon icon="user" className="white-text pt-3 pb-3" />{" "}
                        Login Account
                      </h3>
                      <hr className="hr-light" />
                    </div>
                    <form onSubmit={handleSubmit}>
                      <MDBInput
                        label="Your email"
                        icon="envelope"
                        type="email"
                        name="email"
                        labelClass="white-text"
                        iconClass="white-text"
                        required
                      />
                      <MDBInput
                        label="Your password"
                        icon="lock"
                        type="password"
                        name="password"
                        labelClass="white-text"
                        iconClass="white-text"
                        required
                      />

                      <div className="text-center mt-4">
                        <MDBBtn
                          type="submit"
                          color="primary"
                          rounded
                          className="w-50 mb-1"
                        >
                          LOGIN
                        </MDBBtn>

                        <hr className="hr-light mb-3 mt-4" />
                        <a href="/register" className="white-text mt-2">
                          Don't have an account?
                        </a>
                      </div>
                    </form>
                  </MDBCardBody>
                </MDBCard>
              </MDBCol>
            </MDBRow>
          </MDBContainer>
        </MDBMask>
      </MDBView>
    </section>
  );
}
