import {
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import find from "../../../../services/utilities/axioKit/find";

export default function Scores() {
  const { query } = useSelector(({ scores }) => scores);
  const { token, auth } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();
  const [scores, setScores] = useState([]);
  const [isExamScores, setIsExamScores] = useState(false);

  useEffect(() => {
    if (auth.role !== "EXAMINEE") {
      if (query.key === "exam") {
        setIsExamScores(true);
      }
    }
  }, [query, auth]);

  useEffect(() => {
    if (query) {
      const handleScores = async () => {
        setScores(
          await find("scores", "find", query.key, query.value?._id, token)
        );
      };

      handleScores();

      localStorage.setItem("query", JSON.stringify(query));
    }

    if (auth.role === "EXAMINEE") {
      const handleScores = async () => {
        setScores(await find("scores", "find", "examinee", auth._id, token));
      };

      handleScores();
    }
  }, [dispatch, token, query, auth]);

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-3 mx-4 mb-3 d-flex justify-content-center align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            {auth.role === "EXAMINEE"
              ? "Scores History"
              : ` ${
                  isExamScores
                    ? " " + query.value?.title + " Examination"
                    : " Examinee (" + query.value?.email + ")"
                } 
            Scores`}
          </a>
          {auth.role !== "EXAMINEE" && <div className="p-3"></div>}
        </MDBView>
        <MDBCardBody>
          {scores.length > 0 ? (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th className="th-lg">
                    {auth.role === "EXAMINEE"
                      ? "Exam"
                      : `${isExamScores ? "Examinee" : "Exam"}`}
                  </th>
                  <th className="th-lg">Score</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {scores.map((score, index) => (
                  <tr key={index}>
                    <td>
                      {auth.role === "EXAMINEE"
                        ? scores[index].exam?.title
                        : `${
                            isExamScores
                              ? scores[index].examinee?.email
                              : scores[index].exam?.title
                          }`}
                    </td>
                    <td>
                      {score.score}/{score.bank}
                    </td>
                  </tr>
                ))}
              </MDBTableBody>
            </MDBTable>
          ) : (
            <div className="text-center">Scores Not Found..</div>
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
