import axios from "axios";

const destroy = async (entity, id, token) =>
  await axios
    .delete(`${entity}/${id}/destroy`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    .then(() => {
      return true;
    })
    .catch(({ response }) => {
      const { error } = response.data;
      throw new Error(error);
    });

export default destroy;
