import React, { useState, useEffect } from "react";
import {
  MDBBadge,
  MDBBtn,
  MDBCardBody,
  MDBCol,
  MDBDatePicker,
  MDBIcon,
  MDBRow,
  MDBSelect,
  MDBSelectInput,
  MDBSelectOption,
  MDBSelectOptions,
  MDBSimpleChart,
  MDBTooltip,
  MDBView,
} from "mdbreact";
import { Line } from "react-chartjs-2";
import { monthYearFormat } from "../../../../services/utilities/timeAndDateFormat";
import { distinctFiltering } from "../../../../services/utilities";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE } from "../../../../services/redux/slices/appointments";

export default function Statistics() {
  const { token } = useSelector(({ persons }) => persons);
  const { appointments } = useSelector(({ appointments }) => appointments);
  const [labels, setLabels] = useState([]);
  const [data, setData] = useState([]);
  const [dataRange, setDataRange] = useState("0");
  const [customDateRange, setCustomDateRange] = useState({
    from: null,
    to: null,
  });
  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  const handleCustomDateChange = (date, field) => {
    setCustomDateRange((prevRange) => ({
      ...prevRange,
      [field]: date,
    }));
  };

  useEffect(() => {
    if (customDateRange.from && customDateRange.to) {
      const filteredAppointments = appointments.filter((appointment) => {
        const createdAtDate = new Date(appointment.createdAt);
        return (
          createdAtDate >= customDateRange.from &&
          createdAtDate <= customDateRange.to
        );
      });

      const { labels, data } = distinctFiltering(
        filteredAppointments,
        "createdAt"
      );

      setData(data);
      setLabels(labels);
    }
  }, [customDateRange, appointments]);

  useEffect(() => {
    const _dataRange = Number(dataRange);

    if (_dataRange > 0 && appointments.length > 0) {
      let filteredAppointments = [];

      switch (_dataRange) {
        case 2:
          filteredAppointments = appointments.filter((appointment) => {
            const createdAtDate = new Date(appointment.createdAt);
            const createdAtYear = createdAtDate.getFullYear();
            const currentYear = new Date().getFullYear();
            const lastYear = currentYear - 1;

            return (
              (createdAtYear === currentYear || createdAtYear === lastYear) &&
              createdAtDate.getMonth() >= 0 &&
              createdAtDate.getMonth() <= 11
            );
          });
          break;

        default:
          filteredAppointments = appointments;

          break;
      }

      const { labels, data } = distinctFiltering(
        filteredAppointments,
        "createdAt",
        _dataRange === 2 ? monthYearFormat : null
      );

      setData(data);
      setLabels(labels);
    }
  }, [dataRange, appointments]);

  useEffect(() => {
    if (appointments.length > 0) {
      const { labels, data } = distinctFiltering(appointments, "createdAt");

      setData(data);
      setLabels(labels);
    }
  }, [appointments]);
  return (
    <section>
      <MDBRow>
        <MDBCol xl="5" lg="12" md="6" className="mr-0">
          <div className="view view-cascade gradient-card-header light-blue lighten-1">
            <h2 className="h2-responsive mb-0">Appointments</h2>
          </div>

          <MDBCardBody className="row pt-3">
            <MDBCol md="6" style={{ paddingLeft: "2.3rem" }}>
              <p className="lead">
                <span className="badge info-color p-2 mt-4">Data range</span>
              </p>
              <MDBSelect getValue={(e) => setDataRange(e[0])}>
                <MDBSelectInput selected="Daily" />
                <MDBSelectOptions>
                  <MDBSelectOption disabled>Choose time period</MDBSelectOption>

                  <MDBSelectOption value="1">Daily</MDBSelectOption>
                  <MDBSelectOption value="2">Monthly</MDBSelectOption>
                </MDBSelectOptions>
              </MDBSelect>

              <p className="lead">
                <span className="badge info-color p-2 mt-3 ">Custom date</span>
              </p>

              <div className="mb-1" style={{ marginTop: "2rem" }}>
                <MDBRow>
                  <MDBCol size="12" className="my-2">
                    <small className="grey-text">from:</small>
                    <MDBDatePicker
                      className="my-0 d-inline ml-3"
                      getValue={(date) => handleCustomDateChange(date, "from")}
                    />
                  </MDBCol>
                  <MDBCol size="12">
                    <small className="grey-text">to:</small>
                    <MDBDatePicker
                      className="my-0 d-inline ml-3"
                      getValue={(date) => handleCustomDateChange(date, "to")}
                    />
                  </MDBCol>
                </MDBRow>
              </div>
            </MDBCol>
            <MDBCol md="6" className="text-center mt-4">
              <div style={{ marginBottom: "0.5rem" }}>
                Total Appointments: <strong>{appointments.length}</strong>
                <br />
                <MDBTooltip>
                  <MDBBtn color="info" className="btn-sm p2 d-inline">
                    <MDBIcon icon="question" />
                  </MDBBtn>
                  <div>Total Appointments</div>
                </MDBTooltip>
              </div>
              <br />

              <div style={{ marginBottom: "0.5rem" }}>
                Average:{" "}
                <strong>
                  {(appointments.length / labels.length).toFixed(2)}
                </strong>
                <br />
                <MDBTooltip>
                  <MDBBtn color="info" className="btn-sm p2 d-inline">
                    <MDBIcon icon="question" />
                  </MDBBtn>
                  <div className="text-center">
                    Average appointments in the chosen period
                  </div>
                </MDBTooltip>
              </div>
              <br />
              <div className="my-4">
                <MDBSimpleChart
                  strokeColor="red"
                  strokeWidth={3}
                  width={100}
                  height={100}
                  percent={99}
                  labelFontWeight="normal"
                />
              </div>
              <h5>
                <MDBBadge color="red" className="accent-2 p-2">
                  Rate
                  <MDBIcon icon="arrow-circle-right" className="ml-1" />
                </MDBBadge>
                <MDBTooltip>
                  <MDBBtn color="info" className="btn-sm p2 d-inline">
                    <MDBIcon icon="question" />
                  </MDBBtn>
                  <div>Appointments Completion Rate</div>
                </MDBTooltip>
              </h5>
            </MDBCol>
          </MDBCardBody>
        </MDBCol>
        <MDBCol xl="7" lg="12" md="6" className="mb-4">
          <MDBView cascade className="gradient-card-header blue-gradient">
            <Line
              data={{
                labels,
                datasets: [
                  {
                    label: "# of Appointments",

                    backgroundColor: "rgba(255,255,255,0.4)",
                    borderColor: "#fff",
                    pointBackgroundColor: "transparent",
                    borderWidth: 2,
                    pointBorderColor: "#fff",
                    pointBorderWidth: 1,
                    data,
                  },
                ],
              }}
              options={{
                responsive: true,
                maintainAspectRatio: true,
                legend: {
                  labels: {
                    fontColor: "white",
                    fontSize: 18,
                  },
                },
                scales: {
                  xAxes: [
                    {
                      gridLines: {
                        display: true,
                        color: "rgba(255, 255, 255, 0.2)",
                      },
                      ticks: {
                        fontColor: "#fff",
                      },
                    },
                  ],
                  yAxes: [
                    {
                      gridLines: {
                        display: true,
                        color: "rgba(255, 255, 255, 0.2)",
                      },
                      ticks: {
                        fontColor: "#fff",
                      },
                    },
                  ],
                },
              }}
            />
          </MDBView>
        </MDBCol>
      </MDBRow>
    </section>
  );
}
