import React, { useEffect, useState } from "react";
import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBDataTable,
  MDBIcon,
  MDBTabContent,
  MDBTabPane,
  MDBView,
} from "mdbreact";
import { useDispatch, useSelector } from "react-redux";
import { BROWSE } from "../../../../services/redux/slices/appointments";
import classnames from "classnames";
import Statistics from "./statistics";

const tabItems = [
  { value: "Approved", icon: "check" },
  { value: "Denied", icon: "times" },
];

export default function Dashboard() {
  const { appointments } = useSelector(({ appointments }) => appointments);
  const { token } = useSelector(({ persons }) => persons);

  const [activeItem, setActiveItem] = useState(0);
  const [statusData, setStatusData] = useState({
    approved: [],
    denied: [],
  });

  const dispatch = useDispatch();

  const toggle = (tab) => {
    if (activeItem !== tab) {
      setActiveItem(tab);
    }
  };

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));
    }
  }, [dispatch, token]);

  useEffect(() => {
    const newStatusData = {};

    tabItems.forEach((tabItem) => {
      newStatusData[tabItem.value.toLocaleLowerCase()] = appointments.filter(
        (appointment) => appointment.status === tabItem.value
      );
    });

    setStatusData(newStatusData);
  }, [appointments]);

  return (
    <MDBContainer fluid>
      <MDBCard narrow>
        <Statistics />
        <MDBCardBody>
          <MDBView
            cascade
            className="gradient-card-header blue-gradient narrower py-2 mx-2 mt-3 d-flex justify-content-between align-items-center"
          >
            <a href="#!" className="white-text">
              Status: {tabItems[activeItem].value}
            </a>

            <div className="d-flex">
              {tabItems.map((item, index) => (
                <MDBBtn
                  key={`btn-${index}`}
                  outline
                  rounded
                  color="white"
                  size="sm"
                  className={`px-2 ${classnames({
                    active: activeItem === index,
                  })}`}
                  onClick={() => toggle(index)}
                >
                  <MDBIcon icon={item.icon} className="mt-0" />
                </MDBBtn>
              ))}
            </div>
          </MDBView>

          <MDBTabContent activeItem={activeItem}>
            {tabItems.map((item, index) => (
              <MDBTabPane key={`tabpane-${index}`} tabId={index}>
                <MDBDataTable
                  responsive
                  striped
                  bordered
                  hover
                  data={{
                    columns: [
                      {
                        label: "Examinee",
                        field: "examinee",
                        sort: "asc",
                      },
                      {
                        label: "Schedule",
                        field: "schedule",
                        sort: "asc",
                      },
                      {
                        label: "Exam",
                        field: "exam",
                        sort: "asc",
                      },
                      ...(item.value === "Denied"
                        ? [
                            {
                              label: "Reason",
                              field: "reason",
                              sort: "asc",
                            },
                          ]
                        : []),
                      {
                        label: "Proctor",
                        field: "admin",
                        sort: "asc",
                      },
                    ],
                    rows: statusData[item.value.toLowerCase()].map(
                      (appointment) => ({
                        examinee: appointment.examinee?.email,
                        schedule: appointment.schedule?.title,
                        exam: appointment.schedule?.exam?.title,
                        ...(item.value === "Denied"
                          ? {
                              reason: appointment.reason,
                            }
                          : {}),
                        admin: appointment.schedule?.admin?.username,
                      })
                    ),
                  }}
                />
              </MDBTabPane>
            ))}
          </MDBTabContent>
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
