import {
  MDBBtn,
  MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
} from "mdbreact";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { UPDATE } from "../../../../services/redux/slices/appointments";
import Swal from "sweetalert2";

export default function DenyAppointment({ modal, toggle, appointment }) {
  const { token } = useSelector(({ persons }) => persons);
  const dispatch = useDispatch();

  const [reason, setReason] = useState("");

  const handleDeny = () => {
    Swal.fire({
      icon: "warning",
      html:
        "<h4 style='font-weight: bold;'>Are you sure?</h4>" +
        "<h6 style='font-weight: 400;'>You want to deny this appointment?</h6>",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(
          UPDATE({
            updateFields: {
              _id: appointment._id,
              status: "Denied",
              reason,
            },
            token,
          })
        );
        toggle();
        setReason("");
      }
    });
  };

  return (
    <MDBModal
      size="md"
      className="modal-notify modal-warning"
      backdrop={true}
      isOpen={modal}
      toggle={() => {}}
    >
      <MDBModalHeader
        toggle={toggle}
        titleClass="heading lead"
        className="d-flex justify-content-center"
      >
        <div
          style={{ fontWeight: 450, fontFamily: "inherit", fontSize: "21px" }}
        >
          Please State Your Reason For Denying This Appointment
        </div>
      </MDBModalHeader>

      <MDBModalBody>
        <MDBInput
          type="textarea"
          label="Reason"
          value={reason}
          onChange={(e) => setReason(e.target.value)}
        />
        <MDBBtn size="sm" color="danger" onClick={handleDeny}>
          Deny
        </MDBBtn>
      </MDBModalBody>
    </MDBModal>
  );
}
