import { MDBBtn, MDBBtnGroup } from "mdbreact";
import React from "react";
import { useDispatch } from "react-redux";
import { SELECTED } from "../../../../services/redux/slices/schedules";
import {
  timeFormat,
  monthDayYearFormat,
} from "../../../../services/utilities/timeAndDateFormat";
import { useState } from "react";
import { useEffect } from "react";

export default function RowSchedule({ schedule, toggle, index }) {
  const dispatch = useDispatch();
  const [remainingSlots, setRemainingSlots] = useState(0);

  useEffect(() => {
    setRemainingSlots(schedule.maxSlots - schedule.slotsTaken);
  }, [schedule]);

  const handleUpdate = () => {
    toggle();
    dispatch(SELECTED(schedule));
  };

  return (
    <tr>
      <td>{index + 1}.</td>
      <td>{schedule.title}</td>
      <td>
        {monthDayYearFormat(schedule.start)} <br />
        {timeFormat(schedule.start)}
      </td>
      <td>
        {monthDayYearFormat(schedule.end)} <br />
        {timeFormat(schedule.end)}
      </td>
      <td>{schedule.exam?.title}</td>
      <td>{schedule.admin?.username}</td>
      <td>
        {remainingSlots}/{schedule.maxSlots}
      </td>
      <td>
        <MDBBtnGroup>
          <MDBBtn color="warning" size="sm" onClick={handleUpdate}>
            Update
          </MDBBtn>
        </MDBBtnGroup>
      </td>
    </tr>
  );
}
