import axios from "axios";

const find = async (entity, endpoint, key = "", value = "", token) =>
  await axios
    .get(`${entity}/${endpoint}?${key}=${value}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    .then(({ data }) => {
      const { payload } = data;
      return payload;
    })
    .catch(({ response }) => {
      const { error } = response.data;
      throw new Error(error);
    });

export default find;
