import { MDBBtn, MDBIcon } from "mdbreact";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Swal from "sweetalert2";
import { UPDATE } from "../../../../services/redux/slices/appointments";
import DenyAppointment from "./deny";

export default function RowAppointment({ appointment }) {
  const { token } = useSelector(({ persons }) => persons);
  const [modal, setModal] = useState(false);

  const toggle = () => {
    setModal(!modal);
  };

  const dispatch = useDispatch();

  const handleApproved = () => {
    Swal.fire({
      icon: "info",
      html:
        "<h4 style='font-weight: bold;'>Are you sure?</h4>" +
        "<h6 style='font-weight: 400;'>You want to approve this appointment?</h6>",
      showCancelButton: true,
      confirmButtonText: "Proceed",
    }).then((result) => {
      if (result.isConfirmed) {
        dispatch(
          UPDATE({
            updateFields: {
              _id: appointment._id,
              status: "Approved",
            },
            token,
          })
        );
      }
    });
  };

  return (
    <tr>
      <td>{appointment.examinee?.email}</td>
      <td>{appointment.schedule?.title}</td>
      <td>{appointment.schedule?.exam?.title}</td>
      <td>{appointment.schedule?.admin?.username}</td>
      <td>
        <MDBBtn
          outline
          rounded
          color="success"
          size="sm"
          className="px-2"
          onClick={handleApproved}
        >
          <MDBIcon icon="check" />
        </MDBBtn>
        <MDBBtn
          outline
          rounded
          color="danger"
          size="sm"
          className="px-2"
          onClick={() => toggle()}
        >
          <MDBIcon icon="times" />
        </MDBBtn>
        <DenyAppointment
          modal={modal}
          toggle={toggle}
          appointment={appointment}
        />
      </td>
    </tr>
  );
}
