import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTabContent,
  MDBTabPane,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBView,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import classnames from "classnames";
import RowAppointment from "./row";
import find from "../../../../services/utilities/axioKit/find";

const tabItems = [
  { value: "Pending", icon: "clock" },
  { value: "Approved", icon: "thumbs-up" },
  { value: "Denied", icon: "thumbs-down" },
  { value: "Completed", icon: "check" },
  { value: "Failed", icon: "times" },
];

export default function Appointments() {
  const [examineeAppointments, setExamineeAppointments] = useState([]);
  const { token, auth } = useSelector(({ persons }) => persons);
  const [activeItem, setActiveItem] = useState(0);
  const [statusData, setStatusData] = useState({
    pending: [],
    approved: [],
    denied: [],
    completed: [],
    failed: [],
  });

  const toggle = (tab) => {
    if (activeItem !== tab) {
      setActiveItem(tab);
    }
  };

  useEffect(() => {
    if (token && auth) {
      const handleAppointments = async () => {
        setExamineeAppointments(
          await find("appointments", "find", "examinee", auth._id, token)
        );
      };

      handleAppointments();

      localStorage.removeItem("selectedAppointment");
      localStorage.removeItem("selectedSchedule");
      localStorage.removeItem("selectedExam");
    }
  }, [token, auth]);

  useEffect(() => {
    const newStatusData = {};

    tabItems.forEach((tabItem) => {
      newStatusData[tabItem.value?.toLocaleLowerCase()] =
        examineeAppointments.filter(
          (appointment) => appointment.status === tabItem.value
        );
    });

    setStatusData(newStatusData);
  }, [examineeAppointments]);

  return (
    <MDBContainer fluid>
      <MDBCard narrow>
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text">
            Status: {tabItems[activeItem].value}
          </a>

          <div className="d-flex">
            {tabItems.map((item, index) => (
              <MDBBtn
                key={`btn-${index}`}
                outline
                color="white"
                rounded
                size="sm"
                className={`px-2 ${classnames({
                  active: activeItem === index,
                })}`}
                onClick={() => toggle(index)}
              >
                <MDBIcon icon={item.icon} className="mt-0" />
              </MDBBtn>
            ))}
          </div>
        </MDBView>
        <MDBCardBody>
          <MDBTabContent activeItem={activeItem}>
            {tabItems.map((item, index) => (
              <MDBTabPane key={`tabpane-${index}`} tabId={index}>
                {statusData[item.value?.toLowerCase()]?.length === 0 ? (
                  <div className="text-center">
                    No {item.value} Appointments Yet..
                  </div>
                ) : (
                  <MDBTable responsive hover className="text-center">
                    <MDBTableHead>
                      <tr>
                        <th>#</th>
                        <th className="th-lg">Schedule</th>
                        <th className="th-lg">Start</th>
                        <th className="th-lg">End</th>
                        <th className="th-lg">Exam</th>
                        <th className="th-lg">Proctor</th>
                        {item.value === "Denied" && (
                          <>
                            <th className="th-lg">Reason</th>
                            <th className="th-lg">Action</th>
                          </>
                        )}
                        {item.value === "Approved" && <th>Action</th>}
                      </tr>
                    </MDBTableHead>

                    <MDBTableBody>
                      {statusData[item.value?.toLowerCase()].map(
                        (appointment, index) => (
                          <RowAppointment
                            key={`tbody-${index}`}
                            appointment={appointment}
                            index={index}
                            item={item}
                          />
                        )
                      )}
                    </MDBTableBody>
                  </MDBTable>
                )}
              </MDBTabPane>
            ))}
          </MDBTabContent>
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
