import axios from "axios";

const save = async (entity, email, password) =>
  await axios
    .get(`${entity}/login?email=${email}&password=${password}`)
    .then(({ data }) => {
      const { payload } = data;
      return payload;
    })
    .catch(({ response }) => {
      const { error } = response.data;
      throw new Error(error);
    });

export default save;
