import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import { axioKit, socket } from "../../utilities";

const name = "schedules";

const initialState = {
  schedules: [],
  isSuccess: false,
  isLoading: false,
  message: "",
  _id: "",
  selectedSchedule:
    JSON.parse(localStorage.getItem("selectedSchedule")) || null,
  index: -1,
};

export const BROWSE = createAsyncThunk(`${name}`, (token, thunkAPI) => {
  try {
    return axioKit.browse(name, token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const SAVE = createAsyncThunk(`${name}/save`, (form, thunkAPI) => {
  try {
    return axioKit.save(name, form.schedule, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const UPDATE = createAsyncThunk(`${name}/update`, (form, thunkAPI) => {
  try {
    return axioKit.update(name, form.schedule, form.token);
  } catch (error) {
    const message =
      (error.response && error.response.data && error.response.data.message) ||
      error.message ||
      error.toString();

    return thunkAPI.rejectWithValue(message);
  }
});

export const reduxSlice = createSlice({
  name,
  initialState,
  reducers: {
    RESET: (state, data) => {
      state.isSuccess = false;
      state.message = "";
    },
    SETID: (state, data) => {
      state._id = data.payload;
    },
    SELECTED: (state, data) => {
      state.selectedSchedule = data.payload;
      localStorage.setItem("selectedSchedule", JSON.stringify(data.payload));
      const index = state.schedules.findIndex(
        (schedule) => schedule._id === data.payload._id
      );
      state.index = index;
    },
    UPDATEINDEX: (state, data) => {
      state.index = data.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(BROWSE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(BROWSE.fulfilled, (state, action) => {
        state.schedules = action.payload;
        state.isSuccess = true;
        state.isLoading = false;
      })
      .addCase(BROWSE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(SAVE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(SAVE.fulfilled, (state, action) => {
        state.schedules.unshift(action.payload);
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(SAVE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      })

      .addCase(UPDATE.pending, (state) => {
        state.isLoading = true;
        state.isSuccess = false;
        state.message = "";
      })
      .addCase(UPDATE.fulfilled, (state, action) => {
        const _index = state.schedules.findIndex(
          (schedule) => schedule._id === action.payload._id
        );
        state.schedules[_index] = action.payload;
        socket.emit("start_exam", { _index, _schedule: action.payload });
        state.isLoading = false;
        state.isSuccess = true;
      })
      .addCase(UPDATE.rejected, (state, action) => {
        const { error } = action;
        state.message = error.message;
        state.isLoading = false;
      });
  },
});

export const { RESET, SETID, SELECTED, UPDATEINDEX } = reduxSlice.actions;

export default reduxSlice.reducer;
