import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBTooltip,
  MDBView,
} from "mdbreact";
import RowExam from "./row";
import { useDispatch, useSelector } from "react-redux";
import CreateExam from "./create";
import React, { useEffect, useState } from "react";
import { BROWSE } from "../../../../services/redux/slices/exams";

export default function Exams() {
  const { exams } = useSelector(({ exams }) => exams);
  const [modal, setModal] = useState(false);
  const { token } = useSelector(({ persons }) => persons);

  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(BROWSE(token));

      localStorage.removeItem("selectedExam");
      localStorage.removeItem("query");
    }
  }, [dispatch, token]);

  const toggle = () => {
    setModal(!modal);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <a href="#!" className="white-text mx-3">
            Listed Exams
          </a>
          <div className="text-right">
            <MDBTooltip placement="left">
              <MDBBtn
                outline
                color="white"
                rounded
                size="sm"
                className="px-2"
                onClick={toggle}
              >
                <MDBIcon icon="plus" className="mt-0" />
              </MDBBtn>
              <div>Add</div>
            </MDBTooltip>
            <CreateExam modal={modal} toggle={toggle} />
          </div>
        </MDBView>
        <MDBCardBody>
          {exams.length === 0 ? (
            <div className="text-center">Exams Not Found..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th className="th-lg">Title</th>
                  <th className="th-lg">Description</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {exams.map((exam, index) => (
                  <RowExam key={index} exam={exam} toggle={toggle} />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
