import axios from "axios";

const save = async (entity, data, token) =>
  await axios
    .post(`${entity}/save`, data, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
    })
    .then(({ data }) => {
      const { payload } = data;
      return payload;
    })
    .catch(({ response }) => {
      const { error } = response.data;
      throw new Error(error);
    });

export default save;
