import { MDBCard, MDBView } from "mdbreact";
import { Line } from "react-chartjs-2";
import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { STATISTICS } from "../../../../../services/redux/slices/scores";

export default function PassingPercentages() {
  const { token } = useSelector(({ persons }) => persons);
  const [labels, setLabels] = useState([]);
  const [data, setData] = useState([]);
  const { passingPercentages } = useSelector(({ scores }) => scores);
  const dispatch = useDispatch();

  useEffect(() => {
    if (token) {
      dispatch(STATISTICS(token));
    }
  }, [token, dispatch]);

  useEffect(() => {
    if (passingPercentages) {
      const chartLabels = passingPercentages.map(
        (passingPercentage) => passingPercentage.exam
      );
      const chartData = passingPercentages.map(
        (passingPercentage) => passingPercentage.percentage
      );

      setLabels(chartLabels);
      setData(chartData);
    }
  }, [passingPercentages]);

  return (
    <section className="mb-5 pt-5">
      <MDBCard narrow className="mb-5">
        <MDBView cascade className="gradient-card-header blue-gradient mb-4">
          <Line
            data={{
              labels,
              datasets: [
                {
                  label: "% of Passing Per Exam",
                  data,
                  fillColor: "rgba(220,220,220,0.2)",
                  strokeColor: "rgba(220,220,220,1)",
                  pointColor: "rgba(220,220,220,1)",
                  pointStrokeColor: "#fff",
                  pointHighlightFill: "#fff",
                  pointHighlightStroke: "rgba(220,220,220,1)",
                  backgroundColor: "rgba(255, 255, 255, 0.2)",
                  borderColor: "#fff",
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              responsive: true,
              maintainAspectRatio: true,
              legend: {
                labels: {
                  fontColor: "white",
                  fontSize: 16,
                },
              },
              scales: {
                xAxes: [
                  {
                    gridLines: {
                      display: true,
                      color: "rgba(255, 255, 255, 0.2)",
                    },
                    ticks: {
                      fontColor: "#fff",
                    },
                  },
                ],
                yAxes: [
                  {
                    gridLines: {
                      display: true,
                      color: "rgba(255, 255, 255, 0.2)",
                    },
                    ticks: {
                      fontColor: "#fff",
                      callback: function (value) {
                        return value + "%"; // Add the percentage symbol
                      },
                    },
                  },
                ],
              },
            }}
            height={200}
          />
        </MDBView>
      </MDBCard>
    </section>
  );
}
