import { MDBBtn, MDBIcon, MDBTooltip } from "mdbreact";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { SETID } from "../../../../services/redux/slices/schedules";
import CreateAppointment from "./appointments";

export default function RowExam({ exam }) {
  const dispatch = useDispatch();
  const [modal, setModal] = useState(false);

  const toggle = () => {
    setModal(!modal);
  };

  const handleCreateAppointment = () => {
    dispatch(SETID(exam._id));
    toggle();
  };

  return (
    <tr>
      <td>{exam.title}</td>
      <td>{exam.description}</td>
      <td>
        <MDBTooltip placement="right">
          <MDBBtn
            outline
            color="blue"
            rounded
            size="sm"
            className="px-2"
            onClick={handleCreateAppointment}
          >
            <MDBIcon icon="arrow-right" className="mt-0" />
          </MDBBtn>
          <div>Create Appointment</div>
        </MDBTooltip>
        <CreateAppointment modal={modal} toggle={toggle} />
      </td>
    </tr>
  );
}
