import { configureStore } from "@reduxjs/toolkit";

import persons from "./slices/persons";
import exams from "./slices/exams";
import banks from "./slices/banks";
import scores from "./slices/scores";
import schedules from "./slices/schedules";
import appointments from "./slices/appointments";

const store = configureStore({
  reducer: {
    persons,
    exams,
    banks,
    scores,
    schedules,
    appointments,
  },
});

export default store;
