import {
  MDBBtn,
  MDBContainer,
  MDBInput,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
} from "mdbreact";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  SAVE,
  UPDATE,
  UPDATEINDEX,
} from "../../../../services/redux/slices/exams";

export default function CreateExam({ modal, toggle }) {
  const [exam, setExam] = useState({
    title: "",
    description: "",
  });

  const { isLoading, index, selectedExam } = useSelector(({ exams }) => exams);
  const { token } = useSelector(({ persons }) => persons);
  const [willUpdate, setWillUpdate] = useState(false);

  useEffect(() => {
    if (index >= 0) {
      setWillUpdate(true);
    }
  }, [index]);

  useEffect(() => {
    if (index >= 0) {
      setExam(selectedExam);
    }
  }, [index, selectedExam]);

  const handleChange = (key, value) => {
    setExam({
      ...exam,
      [key]: value,
    });
  };

  const handleReset = () => {
    setExam({
      title: "",
      description: "",
    });
  };

  const dispatch = useDispatch();
  const handleSubmit = (e) => {
    e.preventDefault();
    if (willUpdate) {
      dispatch(UPDATE({ exam, token }));
      handleReset();
      dispatch(UPDATEINDEX(-1));
    } else {
      dispatch(SAVE({ exam, token }));
      handleReset();
    }
    toggle();
  };

  const handleToggle = () => {
    handleReset();
    dispatch(UPDATEINDEX(-1));
    toggle();
  };
  return (
    <MDBModal
      size="md"
      className={`modal-notify modal-${willUpdate ? "warning" : "success"}`}
      backdrop={true}
      isOpen={modal}
      toggle={() => {}}
    >
      <MDBModalHeader tag="p" toggle={handleToggle} titleClass="heading lead">
        {willUpdate ? "Update Exam" : "Create Exam"}
      </MDBModalHeader>

      <MDBModalBody>
        <form onSubmit={handleSubmit}>
          <MDBInput
            type="text"
            label="Title"
            value={exam.title}
            onChange={(e) => handleChange("title", e.target.value)}
            required
          />

          <MDBInput
            type="text"
            label="Description"
            value={exam.description}
            onChange={(e) => handleChange("description", e.target.value)}
            required
          />

          <MDBContainer className="text-center">
            <MDBBtn
              disabled={isLoading}
              type="submit"
              color={`${willUpdate ? "warning" : "success"}`}
              className="w-40"
            >
              {willUpdate ? "Update" : "Submit"}
            </MDBBtn>
          </MDBContainer>
        </form>
      </MDBModalBody>
    </MDBModal>
  );
}
