import {
  MDBBtn,
  MDBCard,
  MDBCardBody,
  MDBContainer,
  MDBIcon,
  MDBTable,
  MDBTableBody,
  MDBTableHead,
  MDBTooltip,
  MDBView,
} from "mdbreact";
import React, { useEffect } from "react";
import RowBank from "./row";
import { useDispatch, useSelector } from "react-redux";
import CreateBank from "./create";
import { useState } from "react";
import find from "../../../../services/utilities/axioKit/find";

export default function Banks() {
  const { token } = useSelector(({ persons }) => persons);
  const { selectedExam } = useSelector(({ banks }) => banks);
  const [modal, setModal] = useState(false);

  const dispatch = useDispatch();
  const [banks, setBanks] = useState([]);

  useEffect(() => {
    if (selectedExam?._id) {
      const handleBanks = async () => {
        const banks = await find(
          "banks",
          "find",
          "exam",
          selectedExam._id,
          token
        );

        setBanks(banks.sort(() => Math.random() - 0.5));
        localStorage.setItem("selectedExam", JSON.stringify(selectedExam));
      };

      handleBanks();
    }
  }, [dispatch, selectedExam, token]);

  const toggle = () => {
    setModal(!modal);
  };

  return (
    <MDBContainer>
      <MDBCard narrow className="pb-3">
        <MDBView
          cascade
          className="gradient-card-header blue-gradient narrower py-2 mx-4 mb-3 d-flex justify-content-between align-items-center"
        >
          <div></div>
          <a href="#!" className="white-text mx-3">
            {selectedExam.title} Banks ({banks.length} Items)
          </a>
          <div className="text-right">
            <MDBTooltip placement="left">
              <MDBBtn
                outline
                color="white"
                rounded
                size="sm"
                className="px-2"
                onClick={toggle}
              >
                <MDBIcon icon="plus" className="mt-0" />
              </MDBBtn>
              <div>Add</div>
            </MDBTooltip>

            <CreateBank modal={modal} toggle={toggle} />
          </div>
        </MDBView>
        <MDBCardBody>
          {banks.length === 0 ? (
            <div className="text-center">Banks Not Found..</div>
          ) : (
            <MDBTable responsive hover className="text-center">
              <MDBTableHead>
                <tr>
                  <th>#</th>
                  <th className="th-lg">Question</th>
                  <th className="th-lg">Choices</th>
                  <th className="th-lg">Answer</th>
                  <th className="th-lg">Action</th>
                </tr>
              </MDBTableHead>

              <MDBTableBody>
                {banks.map((bank, index) => (
                  <RowBank
                    key={index}
                    bank={bank}
                    toggle={toggle}
                    index={index}
                  />
                ))}
              </MDBTableBody>
            </MDBTable>
          )}
        </MDBCardBody>
      </MDBCard>
    </MDBContainer>
  );
}
